import React,{useEffect, useRef,useState, useCallback} from 'react';
import TitleTextComponent from './TitleTextComponent';
import BlackHoleComponent from './BlackHoleComponent';
import ProjectDetailPopUpComponent from './ProjectDetailPopUpComponent';

import anime from 'animejs/lib/anime.es.js';
import SwiperCore, { Navigation,Pagination,EffectFlip  } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import {useSelector} from 'react-redux';
import {setIsScrollable} from '../actions';
import redux from '../redux'

SwiperCore.use([Navigation,Pagination,EffectFlip]);

const ProjectPage = (props) => {
    const projectList = useSelector(state=>state.projectList);


    //ref
    const swiperContainerRef = useRef(null);
    const swiperRef = useRef(null);


    
    var onGoingAnim = [];

    //state declaration
    const [showProjectDetailPopUp,setShowProjectDetailPopUp] = useState("");
    
    const closeShowProjectDetail = () =>{
        props.allowSwipe(true,true);
        setShowProjectDetailPopUp("");

        redux.dispatch(setIsScrollable(true));
    }

    const playSwiperAnimation = useCallback(() => {

        anime.set(swiperContainerRef.current, {
            opacity: function() { return 0 },
            translateY: function() { return 50 }
        });
    
    
        onGoingAnim.push(anime({ 
            targets: swiperContainerRef.current,
            opacity: 1,
            translateY: 0,
            direction: 'normal',
            easing: "linear",
            duration: 1000,
            loop: false
        }));
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);


    useEffect(()=>{
        swiperRef.current.update();
        return () =>{
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    });


    return (
        <div className="pageContainer" id="projectPage">
            {showProjectDetailPopUp !== "" && <ProjectDetailPopUpComponent closePopUp={closeShowProjectDetail} showingProject={showProjectDetailPopUp}/>}

            <div className="contentContainer">
                <TitleTextComponent shouldPlayAnim={props.shouldPlayAnim} animDoneCallback={playSwiperAnimation} titleText="Project" />

                <div ref={swiperContainerRef} className="swiperContainer" style={{opacity:props.shouldPlayAnim?0:1}}>
                    <Swiper tag="section" wrapperTag='ul' slidesPerView={1}
                        onSwiper={(swiper)=>{swiperRef.current = swiper}}
                        breakpoints={{  
                            // when window width is >= 768px
                            768: {
                                spaceBetween:50,
                                slidesPerView: 3
                            }
                        }}
                        navigation pagination>
                        
                        {projectList.map((project) => (
                            <SwiperSlide key={project.id} tag="li">
                                <div onClick={()=>{props.allowSwipe(false,false);setShowProjectDetailPopUp(project);redux.dispatch(setIsScrollable(false));}} style={{width:"100%",height:"100%",background:"rgba(255, 255, 255, 0.5",cursor:"pointer",padding:10}}>
                                    <img width="100%" height="100%" alt='test' src={project.projectMainImageUrl}/>
                                </div>
                            </SwiperSlide>
                        ))}
                 
                    </Swiper>
                </div>

                
            </div>
            <BlackHoleComponent shouldPlayAnim={props.shouldPlayAnim} />
        </div>
    );
};

export default ProjectPage;
