import React,{useEffect,useRef} from 'react';
import anime from 'animejs/lib/anime.es.js';


const galaxyBackgroundSvg = require('../images/GalaxyBackground.svg');
const galaxyBackgroundPng = require('../images/GalaxyBackground.png');




    

function GalaxyStarAnimateBackgroundComponent() {
    const startBg1 = useRef(null);
    const startBg2 = useRef(null);
    const startBg3 = useRef(null);

    var onGoingAnim = [];

    useEffect(() => {


        anime.set([startBg1.current,startBg2.current,startBg3.current], {
            scaleX: function() { return 0 },
            scaleY: function() { return 0 },
            scaleZ: function() { return 0 },
            opacity: function() { return 1 }
        });
        

        onGoingAnim.push(anime({
            targets: [startBg1.current,startBg2.current,startBg3.current],
            scaleX: 3,
            scaleY: 3,
            scaleZ: 3,
            opacity: 0,
            direction: 'normal',
            duration: 4000,
            delay: anime.stagger(1500),
            easing: 'linear',
            loop: true
        }));

        return () => {
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            }); 
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });


  return (
      <div className="starBackgroundContainer">
            <img src={navigator.userAgent.includes('Firefox')?galaxyBackgroundPng:galaxyBackgroundSvg} ref={startBg1} className="starBackground" id="starBg1" alt="starBg1"   />
            <img src={navigator.userAgent.includes('Firefox')?galaxyBackgroundPng:galaxyBackgroundSvg} ref={startBg2} className="starBackground" id="starBg2" alt="starBg2"style={{transform: 'rotate(-180deg)'}} />
            <img src={navigator.userAgent.includes('Firefox')?galaxyBackgroundPng:galaxyBackgroundSvg} ref={startBg3} className="starBackground" id="starBg3" alt="starBg3" />
      </div>
  );
}

export default GalaxyStarAnimateBackgroundComponent;
