import React,{useRef,useEffect} from 'react';
import anime from 'animejs/lib/anime.es.js';



function LoadingPageComponent(props) {
    const loadingWordRef = useRef(null);

    var onGoingAnim = [];

    
    function playLoadingWordAnimation(){
        
        //Text Animation
        var textWrapper = loadingWordRef.current;
        textWrapper.style.opacity = 1;
        var charIterator = "Loading ..."[Symbol.iterator]();
        
        textWrapper.innerHTML="";
        var spanList = [];
        for (let char of charIterator) {
            var node = document.createElement("SPAN"); 
            node.innerHTML = char!==' '?char:'&nbsp;';
            node.classList.add('letter');
            spanList.push(node);

            textWrapper.appendChild(node);
        }
     

        onGoingAnim
        .push(anime({
            targets: spanList,
            scale: [3,1],
            opacity: [0,1],
            translateZ: 0,
            easing: "easeOutExpo",
            duration: 1200,
            delay: (el, i) => 130*i,
            loop: true
        }));

    }

    //hook to perform animation once UI rendered to the screen
    useEffect(() => {
        playLoadingWordAnimation();

        return () =>{
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });


    return (
        <div id="loadingPageContainer" >
          <div id="spinnerWrapper">
            <div id="spinnerMain">
              <div id="spinnerYellow">
                <div id="spinnerGreen">
                  <div id="spinnerBlue">
                    <div id="spinnerPurple">
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div ref={loadingWordRef} id="loadingWord">Loading ...</div>
          </div>
        </div>
    );
}

export default LoadingPageComponent;
