import React,{useState,useEffect,useContext,useRef} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import CustomFileUpload from "./formikCustomComponent/CustomFileUpload"
import CustomDatePicker from "./formikCustomComponent/CustomDatePicker"
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'




const ExperienceTab = (props) => {
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    const [editingExperience, setEditingExperience] = useState("");

    const experienceList = useSelector(state=>state.experienceList);

    const experienceSelectRef = useRef(null);

    //yup validation custom function
    function checkIfFilesAreTooBigArray(files){
        let valid = true;
        if (files) {
            for(var file of files){
                const size = file.size / 1024 / 1024;
                if (size > 10) {
                    valid = false;
                }
            };
        }
        return valid;
    }

    function checkIsImageArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['image/svg+xml', 'image/jpeg', 'image/png'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
    
    //yup validation Schema
    const projectValidationSchema = Yup.object().shape({
        experienceTitle: Yup.string()
            .min(3, 'Experience Title must have at least 3 character!')
            .max(50, 'Experience Title must cannot exceed 50 character!')
            .required('Required'),
        experienceDesc: Yup.string()
            .min(50, 'Experience Desc must have at least 50 character!')
            .max(200, 'Experience Desc cannot exceed 200 character!')
            .required('Required'),
        experienceInstitution: Yup.string()
            .min(3, 'Experience Institution must have at least 3 character!')
            .max(100, 'Experience Institution cannot exceed 100 character!')
            .required('Required'),
        institutionImage: Yup.mixed()
            .nullable()
            .test('at-least-one-file',"Must Select Image", files=>{return files.length>0|| editingExperience !==""?true:false})
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-image-file',
            'File must be image format ex.(png,jpg,svg)',
            checkIsImageArray
        ),
        experienceStartDate: Yup.string()
            .required('Required'),
        experienceEndDate: Yup.string()
            .required('Required'),
      });

    useEffect(()=>{
        if(editingExperience !== ""){
             //update editting experience
             experienceList.forEach((experience)=>{if(experience.id === editingExperience.id){setEditingExperience(experience)}});
        }

        return () =>{
            
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[experienceList]);

    const deleteFromDatabase = ()=>{
        //show loading dialog
        Swal.fire({
            title: 'Uploading To Firebase',
            html: 'We are processing your request.',
            allowEscapeKey: false,
            allowOutsideClick: false,
            timerProgressBar: true,
            willOpen: () => {
                Swal.showLoading()
            }
        });

        firebaseContext.deleteExperienceToFirebase(editingExperience.id,(success)=>{
            Swal.close();
            if(success){
                Swal.fire({
                    icon: 'success',
                    title: 'Successfully Deleted to Firebase',
                    text: 'Deleted the Education in firebase'
                });

                setEditingExperience("");
            }else{
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
            }
        });
    }

    const findExperienceWithExperienceId = (id)=>{
        var foundExperience = "";
        experienceList.map((experience)=>{  
            if(experience.id === id){
                foundExperience = experience;
            }
            return null;
        });
        return foundExperience;
    }

    return (
        <div className="container-fluid" style={{marginTop: 20,paddingBottom: 200}}>
            <Formik
            initialValues={{
                experienceTitle: editingExperience ===""?'':editingExperience.experienceTitle,
                experienceDesc: editingExperience ===""?'':editingExperience.experienceDesc,
                experienceInstitution:  editingExperience ===""?'':editingExperience.experienceInstitution,
                experienceStartDate:  editingExperience ===""?'':editingExperience.experienceStartDate,
                experienceEndDate:  editingExperience ===""?'':editingExperience.experienceEndDate,
                institutionImage: []
            }}
            validationSchema={projectValidationSchema}
            onSubmit={(values,{ resetForm }) => {
          
                //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                });

                if(editingExperience === ""){
                    firebaseContext.addExperienceToFirebase(values.experienceTitle,values.experienceDesc,values.experienceInstitution,values.institutionImage,values.experienceStartDate,values.experienceEndDate,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Successfully added the Experience to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }else{
                    firebaseContext.editExperienceToFirebase(editingExperience.id,values.experienceTitle,values.experienceDesc,values.experienceInstitution,values.institutionImage,values.experienceStartDate,values.experienceEndDate,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Updated the Experience detail to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }

            }}
            enableReinitialize>
                {({ errors, touched,values,setFieldValue,setFieldTouched }) => (
                    <Form>
                         <div className="form-group">
                            <label >List of Experience <input type="button" value="Add New" onClick={()=>{setEditingExperience("")}}/></label>
                            <select ref={experienceSelectRef} onChange={()=>{setEditingExperience(findExperienceWithExperienceId(experienceSelectRef.current.value));}} multiple className="form-control" >
                                {
                                    experienceList.map((experience)=>(
                                        <option key={experience.id} value={experience.id} >{experience.experienceTitle}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="input-title" ><u>{editingExperience ===""?'Add Experience':`Editing Experience: ${editingExperience.id}`}</u></div>
                        
                        <div className="input-title" >Experience Title</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='experienceTitle'  className="form_input inputStyle" placeholder="Experience Title"/>
                        </div>
                        {touched.experienceTitle && <span className="errorTextStyle" >{errors.experienceTitle}</span>}

                        <div className="input-title">Experience Desc</div>
                        <Field name='experienceDesc' component="textarea" className="form-control input-text-area-style mt-10" style={{marginTop: 20}} rows="5" id="comment" />
                        {touched.experienceDesc && <span className="errorTextStyle" >{errors.experienceDesc}</span>}


                        <div className="input-title">Experience Institution</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='experienceInstitution'  className="form_input inputStyle" placeholder="Experience Insitution"/>
                        </div>
                        {touched.experienceInstitutionAndDate && <span className="errorTextStyle" >{errors.experienceInstitutionAndDate}</span>}
                        
                        <div className="input-title">Experience Start and End Date</div>
                        <div style={{width:"100%",minHeight: 40}}>
                            <CustomDatePicker type="text" name='experienceStartDate'  value={values.experienceStartDate} error={errors.experienceStartDate}  onChange={setFieldValue} className="form_input inputStyle" placeholder="Experience Start Date"/>
                            <div style={{ height: 30,display:"inline-block",verticalAlign:"top",marginLeft:10,marginRight:10}}>To</div>
                            <CustomDatePicker type="text" name='experienceEndDate'  value={values.experienceEndDate}  error={errors.experienceEndDate} onChange={setFieldValue} className="form_input inputStyle" placeholder="Experience Start Date"/>
                        </div>

                        <div className="input-title">Experience Insitution Image</div>
                        <div style={{marginTop: 20}}>
                            <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text">Upload</span>
                            </div>
                            <CustomFileUpload
                                name = 'institutionImage'
                                isMulti={false}
                                value={values.institutionImage}
                                onChange={setFieldValue}
                                error={errors.institutionImage}
                                touched={touched.institutionImage}
                                acceptType="image/svg+xml, image/jpeg, image/png"
                                isEditing = {editingExperience !==""}
                            />
                            </div>
                        </div>
                        {touched.institutionImage && <span className="errorTextStyle" >{errors.institutionImage}</span>}


                        <input type="submit" onClick={()=>{if(Object.keys(errors).length !== 0){Swal.fire({icon: 'error', title: 'Input error',text: 'Please fix all error first!'});}}}className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Edit To Firebase"/>
                        {editingExperience !== "" && <input type="button" className="btn btn-outline-secondary btn-block" onClick={()=>{deleteFromDatabase();}} value="Delete"/>}
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default ExperienceTab;
