import React,{useState,useEffect,useContext,useRef} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import CustomFileUpload from "./formikCustomComponent/CustomFileUpload"
import CustomDatePicker from "./formikCustomComponent/CustomDatePicker"
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'




const EducationTab = (props) => {
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    const [editingEducation, setEditingEducation] = useState("");

    const educationList = useSelector(state=>state.educationList);

    var educationSelectRef = useRef(null);

    //yup validation custom function
    function checkIfFilesAreTooBigArray(files){
        let valid = true;
        if (files) {
            for(var file of files){
                const size = file.size / 1024 / 1024;
                if (size > 10) {
                    valid = false;
                }
            };
        }
        return valid;
    }

    function checkIsImageArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['image/svg+xml', 'image/jpeg', 'image/png'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
    
    //yup validation Schema
    const projectValidationSchema = Yup.object().shape({
        educationTitle: Yup.string()
            .min(3, 'Education Title must have at least 3 character!')
            .max(50, 'Education Title must cannot exceed 50 character!')
            .required('Required'),
        educationDesc: Yup.string()
            .min(50, 'Education Desc must have at least 50 character!')
            .max(200, 'Education Desc cannot exceed 200 character!')
            .required('Required'),
        educationInstitution: Yup.string()
            .min(3, 'Education Institution And Date must have at least 3 character!')
            .max(100, 'Education Institution And Date must cannot exceed 100 character!')
            .required('Required'),
        achievement: Yup.string()
            .min(3, 'Education Achievement must have at least 3 character!')
            .max(100, 'Education Achievement cannot exceed 100 character!')
            .required('Required'),
        institutionImage: Yup.mixed()
            .nullable()
            .test('at-least-one-file',"Must Select Image", files=>{return files.length>0|| editingEducation !==""?true:false})
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-image-file',
            'File must be image format ex.(png,jpg,svg)',
            checkIsImageArray
        ),
        educationStartDate: Yup.string()
            .required('Required'),
        educationEndDate: Yup.string()
            .required('Required'),
      });

    useEffect(()=>{
        if(editingEducation !== ""){
             //update editting skill
             educationList.forEach((education)=>{if(education.id === editingEducation.id){setEditingEducation(education)}});
        }

        return () =>{
            
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[educationList]);

    const deleteFromDatabase = ()=>{
        //show loading dialog
        Swal.fire({
            title: 'Uploading To Firebase',
            html: 'We are processing your request.',
            allowEscapeKey: false,
            allowOutsideClick: false,
            timerProgressBar: true,
            willOpen: () => {
                Swal.showLoading()
            }
        });

        firebaseContext.deleteEducationToFirebase(editingEducation.id,(success)=>{
            Swal.close();
            if(success){
                Swal.fire({
                    icon: 'success',
                    title: 'Successfully Deleted to Firebase',
                    text: 'Deleted the Education in firebase'
                });

                setEditingEducation("");
            }else{
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
            }
        });
    }

    const findEducationWithProjectId = (id)=>{
        var foundEducation = "";
        educationList.map((education)=>{  
            if(education.id === id){
                foundEducation = education;
            }
            return null;
        });
        return foundEducation;
    }

    return (
        <div className="container-fluid" style={{marginTop: 20,paddingBottom: 200}}>
            <Formik
            initialValues={{
                educationTitle: editingEducation ===""?'':editingEducation.educationTitle,
                educationDesc: editingEducation ===""?'':editingEducation.educationDesc,
                educationInstitution:  editingEducation ===""?'':editingEducation.educationInstitution,
                achievement: editingEducation ===""?'':editingEducation.achievement,
                institutionImage: [],
                educationStartDate: editingEducation ===""?'':editingEducation.educationStartDate,
                educationEndDate: editingEducation ===""?'':editingEducation.educationEndDate
            }}
            validationSchema={projectValidationSchema}
            onSubmit={(values,{ resetForm }) => {
          
                //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                });

                if(editingEducation === ""){
                    firebaseContext.addEducationToFirebase(values.educationTitle,values.educationDesc,values.educationInstitution,values.achievement,values.institutionImage,values.educationStartDate,values.educationEndDate,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Successfully added the Education to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }else{
                    firebaseContext.editEducationToFirebase(editingEducation.id,values.educationTitle,values.educationDesc,values.educationInstitution,values.achievement,values.institutionImage,values.educationStartDate,values.educationEndDate,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Updated the Education detail to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }

            }}
            enableReinitialize>
                {({ errors, touched,values,setFieldValue,setFieldTouched }) => (
                    <Form>
                         <div className="form-group">
                            <label >List of Education <input type="button" value="Add New" onClick={()=>{setEditingEducation("")}}/></label>
                            <select ref={educationSelectRef} onChange={()=>{setEditingEducation(findEducationWithProjectId(educationSelectRef.current.value));}}multiple className="form-control" >
                                {
                                    educationList.map((education)=>(
                                        <option key={education.id} value={education.id}>{education.educationTitle}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="input-title" ><u>{editingEducation ===""?'Add Education':`Editing Education: ${editingEducation.id}`}</u></div>
                        
                        <div className="input-title" >Education Title</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='educationTitle'  className="form_input inputStyle" placeholder="Education Title"/>
                        </div>
                        {touched.educationTitle && <span className="errorTextStyle" >{errors.educationTitle}</span>}

                        <div className="input-title">Education Desc</div>
                        <Field name='educationDesc' component="textarea" className="form-control input-text-area-style mt-10" style={{marginTop: 20}} rows="5" id="comment" />
                        {touched.educationDesc && <span className="errorTextStyle" >{errors.educationDesc}</span>}


                        <div className="input-title">Education Institution</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='educationInstitution'  className="form_input inputStyle" placeholder="Education Insitution"/>
                        </div>
                        {touched.educationInstitution && <span className="errorTextStyle" >{errors.educationInstitution}</span>}
                        
                        
                        <div className="input-title">Education Start and End Date</div>
                        <div style={{width:"100%",minHeight: 40}}>
                            <CustomDatePicker type="text" name='educationStartDate'  value={values.educationStartDate} error={errors.educationStartDate}  onChange={setFieldValue} className="form_input inputStyle" placeholder="Education Start Date"/>
                            <div style={{ height: 30,display:"inline-block",verticalAlign:"top",marginLeft:10,marginRight:10}}>To</div>
                            <CustomDatePicker type="text" name='educationEndDate'  value={values.educationEndDate}  error={errors.educationEndDate} onChange={setFieldValue} className="form_input inputStyle" placeholder="Education Start Date"/>
                        </div>
                        

                        <div className="input-title">Achievement</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='achievement'  className="form_input inputStyle" placeholder="Education Achievement"/>
                        </div>
                        {touched.achievement && <span className="errorTextStyle" >{errors.achievement}</span>}

                        <div className="input-title">Education Institution Image</div>
                        <div style={{marginTop: 20}}>
                            <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text">Upload</span>
                            </div>
                            <CustomFileUpload
                                name = 'institutionImage'
                                isMulti={false}
                                value={values.institutionImage}
                                onChange={setFieldValue}
                                error={errors.institutionImage}
                                touched={touched.institutionImage}
                                acceptType="image/svg+xml, image/jpeg, image/png"
                                isEditing = {editingEducation !==""}
                            />
                            </div>
                        </div>
                        {touched.institutionImage && <span className="errorTextStyle" >{errors.institutionImage}</span>}


                        <input type="submit" onClick={()=>{if(Object.keys(errors).length !== 0){Swal.fire({icon: 'error', title: 'Input error',text: 'Please fix all error first!'});}}}className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Edit To Firebase"/>
                        {editingEducation !== "" && <input type="button" className="btn btn-outline-secondary btn-block" onClick={()=>{deleteFromDatabase();}} value="Delete"/>}
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default EducationTab;
