import React,{useEffect, useRef} from 'react';
import anime from 'animejs/lib/anime.es.js';


const purpleMonsterIcon = require('../images/SkillDetailComponent/PurpleMonster.svg');
const lineLeftIcon = require('../images/SkillDetailComponent/lineLeft.svg');
const lineRightIcon = require('../images/SkillDetailComponent/lineRight.svg');
const starFilledIcon = require('../images/SkillDetailComponent/SkillStar_Filled.svg');
const starEmptyIcon = require('../images/SkillDetailComponent/SkillStar_Empty.svg');








const SkillDetailPopUpComponent = (props) => {
    
    const skillDetailPopUpContainerRef = useRef(null);

    var onGoingAnim = [];

    function playShowAnimation(){
        var width = skillDetailPopUpContainerRef.current.clientWidth;

        anime.set(skillDetailPopUpContainerRef.current, {
            translateX: function() { return -1*width*2 },
            translateY: function() { return 0}
        });

        

        //move up and down for floating effect
        onGoingAnim.push(anime({ 
            targets: skillDetailPopUpContainerRef.current,
            translateX:  0,
            translateY: [
                { value: -10, duration: width*6/4 },
                { value: 10, duration: width*6/4},
                { value: -10, duration: width*6/4 },
                { value: 0, duration: width*6/4 }
            ],
            easing: 'linear',
            duration: 2000,
            direction: 'normal',
            loop: false,
            complete: function(anim){
                anime.set(skillDetailPopUpContainerRef.current, {
                    translateY: function() { return -5}
                });

                onGoingAnim.push(anime({ 
                    targets: skillDetailPopUpContainerRef.current,
                    translateY:  5,
                    translateZ: 0.01,
                    easing: 'linear',
                    duration: 1000,
                    direction: 'alternate',
                    loop: true
                }));        
            }
        }));
    }

    function playHideAnimation(){
        var width = skillDetailPopUpContainerRef.current.clientWidth;
    

        //move up and down for floating effect
        onGoingAnim.push(anime({ 
            targets: skillDetailPopUpContainerRef.current,
            translateX:  width *1.5 ,
            translateY: [
                { value: -10 },
                { value: 10},
                { value: -10 },
                { value: 0 }
            ],
            easing: 'linear',
            duration: 2000,
            direction: 'normal',
            loop: false,
            begin: function(currAnim){
                //clear all running anim
                onGoingAnim.forEach(anim=>{
                    if(anim !== currAnim){
                        var index = anime.running.indexOf(anim);
                        if (index !== -1) {
                            anime.running[index].pause();
                            anime.running[index].remove();
                        }
                    }
                });

            },
            complete: function(anim){
                props.closePopUp();
            }
        }));
    }

    useEffect(()=>{
        playShowAnimation();

        return () => {
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running[index].pause();
                    anime.running[index].remove();
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });



    return (
        <div>
            <div className="popUpComponentDarkFilter" />
            <div ref={skillDetailPopUpContainerRef} className="skillDetailPopUpContainer">
                <div className="skillMonsterContainer">
                    <div>
                        <img alt="purple monster" src={purpleMonsterIcon} className="purpleMonsterIcon"/>
                        <img alt="lineleft" src={lineLeftIcon} className="purpleMonsterLine"/>
                    </div>

                    <div>
                    <img alt="lineleft" src={lineRightIcon} className="purpleMonsterLine" style={{marginLeft:0,marginRight:-25}}/>
                        <img alt="purple monster" src={purpleMonsterIcon} className="purpleMonsterIcon" />
                        
                    </div>
                </div>
                <div className="skillDetailContentContainer">
                    <div className="closeButton" onClick={()=>{playHideAnimation();}}>
                        <div className="closeButtonlineOne" />
                        <div className="closeButtonlineTwo" />
                    </div>
                    
                    <div className="skillImageContainer">
                        <div className="skillImageInnerContainer">
                            <img alt="cirleImageFrame" src={props.showingSkill.skillImageUrl} width="100%" height="100%"/>
                        </div>
                    </div>

                    <div className="skillsStarContainer">
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 0?starFilledIcon:starEmptyIcon} className="skillStar"/>
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 1?starFilledIcon:starEmptyIcon} className="skillStar"/>
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 2?starFilledIcon:starEmptyIcon} className="skillStar"/>
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 3?starFilledIcon:starEmptyIcon} className="skillStar"/>
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 4?starFilledIcon:starEmptyIcon} className="skillStar"/>
                        <img alt="skillstar" src={parseInt(props.showingSkill.skillLevel) > 5?starFilledIcon:starEmptyIcon} className="skillStar"/>
                    </div>
                    <hr className="skillDetailLine"/>

                    <div className="skillDetailTitle">{props.showingSkill.skillName}</div>
                    <div className="skillDetailDesc">
                        {props.showingSkill.skillDesc}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SkillDetailPopUpComponent;
