import React,{useEffect,useState} from 'react';
import BlackHoleComponent from './BlackHoleComponent';
import TitleTextComponent from './TitleTextComponent';
import SkillDetailPopUpComponent from './SkillDetailPopUpComponent';
import {useSelector} from 'react-redux'

import anime from 'animejs/lib/anime.es.js';

import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';
import 'tippy.js/themes/material.css';
import {setIsScrollable} from '../actions';
import redux from '../redux'


const SkillsPage = (props) => {
    const skillList  = useSelector(state=>state.skillList);

    //state declaration
    const [showSkillDetailPopUp,setShowSkillDetailPopUp] = useState("");

    
    var onGoingAnim = [];

    const closeShowSkillDetail = () =>{
        props.allowSwipe(true,true);
        setShowSkillDetailPopUp("");
        redux.dispatch(setIsScrollable(true));
    }

    function playSkillsAnimation(){

        anime.set(".skillIcon", {
            translateX: function() { return -200 },
            translateY: function() { return -200 },
            rotate: function() { return 0 },
            opacity: 0
        });

        onGoingAnim.push(anime({ 
            targets: ".skillIcon",
            translateY:0,
            translateX:0,
            opacity:1,
            rotate: 360,
            duration: 5000,
            loop:false,
            delay: anime.stagger(600),
            complete: function(anim){
                var skillIconList = document.getElementsByClassName("skillIcon");

                for(let element of skillIconList){
                    onGoingAnim.push(anime({
                        targets: element,
                        translateY: anime.random(-10,-20),
                        duration: 2000,
                        loop:true,
                        direction:"alternate",
                        easing: "linear"
                    }))
                }

                tippy(document.querySelectorAll(".skillIcon")[0], {
                    placement: 'top', 
                    arrow: true,
                    trigger: 'manual',
                    content: 'Click me',
                    theme: 'light',
                    hideOnClick: false, // if you want
                    onShow(instance) {
                      setTimeout(() => {
                        instance.hide();
                      }, 5000);
                    }
                  }).show();
               
            }
        }));
    }

    useEffect (() =>{
        
        return ()=>{
            onGoingAnim.forEach((anim)=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            })

        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    return (
        <div className="pageContainer">
            {showSkillDetailPopUp !== "" && <SkillDetailPopUpComponent closePopUp={closeShowSkillDetail} showingSkill={showSkillDetailPopUp}/>}

            <div className="contentContainer">
                <TitleTextComponent shouldPlayAnim={props.shouldPlayAnim} animDoneCallback={()=>{playSkillsAnimation();}} titleText="Skills" />

                <div className="skillsContainer">
                    {skillList.map((skill) => (
                        <img key={skill.id} onClick={()=>{props.allowSwipe(false,false);setShowSkillDetailPopUp(skill);redux.dispatch(setIsScrollable(false));}} alt="skillicon" src ={skill.skillImageUrl} className="skillIcon" style={{cursor:"pointer",opacity: props.shouldPlayAnim?0:1,transform:props.shouldPlayAnim?'':'translateX(0) translateY(0)'}} />
                    ))}
                </div>
            </div>
            <BlackHoleComponent shouldPlayAnim={props.shouldPlayAnim} />
        </div>
    );
}

export default SkillsPage;
