import pageEnum from '../enums/pageEnums';

export const setPage = (page) => {
    if(!Object.values(pageEnum).includes(page)){
        throw Error("Page Enum Not Valid");
    }

    return {
        type: 'SETPAGE',
        payload: page
    }
}

export const setEducationList = (educationList) => {

    return {
        type: 'SET_EDUCATION_LIST',
        payload: educationList
    }
}

export const setExperienceList = (experienceList) => {

    return {
        type: 'SET_EXPERIENCE_LIST',
        payload: experienceList
    }
}

export const setProjectList = (projectList) => {

    return {
        type: 'SET_PROJECT_LIST',
        payload: projectList
    }
}

export const setSkillList = (skillList) => {

    return {
        type: 'SET_SKILL_LIST',
        payload: skillList
    }
}

export const setContact = (email,githubUrl,linkedInUrl,resume,resumeUrl) => {

    return {
        type: 'SET_CONTACT',
        payload: {email:email,githubUrl:githubUrl,linkedInUrl:linkedInUrl,resume:resume,resumeUrl:resumeUrl}
    }
}

export const setIsScrollable = (isScrollable) => {

    return {
        type: 'SET_IS_SCROLLABLE',
        payload: isScrollable
    }
}

export const setAbout = (title,desc) => {

    return {
        type: 'SET_ABOUT',
        payload: {title:title,desc:desc}
    }
}