import React from 'react'



class CustomFileUpload extends React.Component {
    handleChange = event => {
      // this is going to call setFieldValue and manually update values.topcis
      this.props.onChange(this.props.name, event.currentTarget.files);
    };

    getFileList = () =>{
      var name = "";

      for(var file of this.props.value){
        name +=file.name+", "
      }

      //for editing project we accept empty
      if(this.props.isEditing === true){
        if(this.props.value.length ===0){
          return "Same As Image Already Uploaded";
        }
      }
      
      return name;
    }  
  
    render() {
      return (
        <div className="custom-file">
            <input type="file" className="custom-file-input" onChange={this.handleChange} multiple={this.props.isMulti} accept={this.props.acceptType} />
            <label className="custom-file-label" ><p >{this.getFileList()}</p></label>
        </div>
      );
    }
  }

  export default CustomFileUpload;