import React,{useEffect,useContext} from 'react';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'
import * as Yup from 'yup';
import { Formik, Form, Field } from 'formik';



const LoginPage = (props) => {
    var firebaseContext= useContext(FirebaseContext);


    useEffect(()=>{
        

        return () =>{
            
        }
    });

    const login = (email,pass)=>{
        //show loading dialog
        Swal.fire({
            title: 'Authenticating Firebase',
            html: 'We are validating your credential.',
            allowEscapeKey: false,
            allowOutsideClick: false,
            timerProgressBar: true,
            willOpen: () => {
                Swal.showLoading()
            }
        });
        // All future sign-in request now include tenant ID.
        firebaseContext.firebaseAuth.signInWithEmailAndPassword(email, pass)
        .then(function(result) {
            Swal.close();
            Swal.fire({
                icon: 'success',
                title: 'Successfully Logged In',
                text: 'You may have access to all dashboard function now'
            });

        }).catch(function(error) {
            Swal.close();

            // Handle error.
            Swal.fire({
                icon: 'error',
                title: 'Invalid Username or Password!',
                text: 'Either username or password is wrong.'
            });

        });
    }

     //yup validation Schema
     const loginValidationSchema = Yup.object().shape({
        email: Yup.string()
            .required('Required'),
            password: Yup.string()
            .required('Required'),
      });
    


    return (
        <div id="login">
            <h1>Login</h1>
            <div className="login-form">
                <Formik
                initialValues={{
                    email: "",
                    password: ""
                }}
                validationSchema={loginValidationSchema}
                onSubmit={(values,{ resetForm }) => {
                    login(values.email,values.password);        
                }}
                enableReinitialize>
                    {({ errors, touched,values,setFieldValue,setFieldTouched }) => (
                        <Form>
                            <div className="username-row row">
                                <Field name="email"type="text" id="username_input" className="username-input"
                                placeholder="Email" />
                            </div>
                            {touched.email && <span className="errorTextStyle" >{errors.email}</span>}

                            <div className="password-row row">
                                <Field name="password" type="password" id="password_input" className="password-input"
                                placeholder="Password" />
                            </div>
                            {touched.password && <span className="errorTextStyle" >{errors.password}</span>}

                            <div className="call-to-action">
                                <button type="submit" id="login-button" value="Log In" >Log In</button>
                            </div>
                        </Form>
                    )}
                </Formik>
            </div>
        </div>   
    );
}

export default LoginPage;
