const {
	abs,
	atan2,
	cos,
	PI,
	pow,
	random,
	sin,
	sqrt,
} = Math
const rand = n => n * random()
const randIn = (_min, _max) => rand(_max - _min) + _min
const fadeInOut = (t, m) => {
	let hm = 0.5 * m
	return abs((t + hm) % m - hm) / hm
}
const angle = (x1, y1, x2, y2) => atan2(y2 - y1, x2 - x1)
const HALF_PI = 0.5 * PI
export const TAU = 2 * PI
const lerp = (a, b, t) => (1 - t) * a + t * b
const fadeOut = (t, m) => (m - t) / m
const dist = (x1, y1, x2, y2) => sqrt(pow(x2 - x1, 2) + pow(y2 - y1, 2))

// eslint-disable-next-line
Array.prototype.lerp = function(t = [], a = 0) {
	this.forEach((n, i) => (this[i] = lerp(n, t[i], a)))
}

function createCanvas (width, height) {
	const canvas = document.createElement('canvas')

	canvas.width = width
	canvas.height = height

	return canvas
}

function createContext2D (width , height , contextAttributes) {
	return createCanvas(width, height).getContext('2d', contextAttributes)
}

function createOffscreenContext2D (width , height , contextAttributes) {
	return createOffscreenCanvas(width, height).getContext('2d', contextAttributes)
}

function createOffscreenCanvas (width, height) {
	let _canvas

	if (typeof OffscreenCanvas !== 'undefined') {
		_canvas = new OffscreenCanvas(parseFloat(width), parseFloat(height))
	} else {
		_canvas = createCanvas(width, height)
	}

	return _canvas
}

export function createRenderingContext (width, height,element) {
	const contextAttributes = {
		desynchronized: true,
		willReadFrequently: true
	}

	const ctx = createContext2D(width, height, contextAttributes)
	const buffer = createOffscreenContext2D(width, height, contextAttributes)

	ctx.canvas.style.position = 'absolute'

    element.appendChild(ctx.canvas)
    

	return {
		buffer,
		ctx
	}
}

const eventHorizon = 40;
const gravStrength = 50;


export class Particle {
    constructor(buffer,center) {
        this.buffer=buffer;
        this.center = center;
        this.init();
    }
    get color() {
        return `hsla(${this.hue}, 50%, 80%, ${fadeInOut(this.life, this.ttl)})`;
    }
    init() {
        this.life = 0;
        this.ttl = randIn(50, 200);
        this.speed = randIn(3, 5);
        this.size = randIn(0.5, 2);
        this.position = [rand(this.buffer.canvas.width), rand(this.buffer.canvas.height)];
        this.lastPosition = [...this.position];
        this.direction = angle(...this.position, ...this.center.current) - HALF_PI;
        this.velocity = [
        cos(this.direction) * this.speed,
        sin(this.direction) * this.speed
        ];
        this.hue = rand(360);
        this.reset = false;
    }
    die() {
        this.buffer.save();
        this.buffer.globalAlpha = 0.1;
        this.buffer.lineWidth = 1;
        this.buffer.strokeStyle = this.color;
        this.buffer.beginPath();
        this.buffer.arc(...this.center.current, eventHorizon, 0, TAU);
        this.buffer.closePath();
        this.buffer.stroke();
        this.buffer.restore();

        this.init();
    }
    update() {
        this.lastPosition = [...this.position];
        this.direction = lerp(
        angle(...this.lastPosition, ...this.center.current),
        angle(...this.position, ...this.center.current),
        0.01
        );
        this.speed = fadeOut(dist(...this.position, ...this.center.current), this.buffer.canvas.width) * gravStrength;
        this.velocity.lerp(
        [cos(this.direction) * this.speed, sin(this.direction) * this.speed],
        0.01
        );
        this.position[0] += this.velocity[0];
        this.position[1] += this.velocity[1];

        this.life++ > this.ttl && this.init();
        dist(...this.position, ...this.center.current) <= eventHorizon && this.die();

        return this;
    }
    draw() {
        this.buffer.save();
        this.buffer.lineWidth = this.size;
        this.buffer.strokeStyle = this.color;
        this.buffer.beginPath();
        this.buffer.moveTo(...this.lastPosition);
        this.buffer.lineTo(...this.position);
        this.buffer.stroke();
        this.buffer.closePath();
        this.buffer.restore();

        return this;
    }
}
