import React,{useEffect, useRef} from 'react';
import anime from 'animejs/lib/anime.es.js';
import SwiperCore, { Navigation,Pagination,EffectFlip  } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'


const CircleImageFrame = require('../images/ProjectDetailComponent/CircleImageFrame.svg');
const rocketIcon = require('../images/ProjectDetailComponent/IC_Rocket.svg');
const meteorIcon = require('../images/ProjectDetailComponent/Meteor.svg');
const redMonsterIcon = require('../images/ProjectDetailComponent/RedMonster.svg');
const downloadReportIcon = require('../images/ProjectDetailComponent/IC_DownloadReport.svg');
const gitHubIcon = require('../images/IC_Github.svg');


SwiperCore.use([Navigation,Pagination,EffectFlip]);


const ProjectDetailPopUpComponent = (props) => {
    const projectDetailContainerRef= useRef(null);
    const monster1Ref= useRef(null);
    const monster2Ref= useRef(null);
    const monsterContainerRef = useRef(null);
    const projectDetailContentContainerRef = useRef(null);
    const projectImageContainerRef = useRef(null);
    const meteor1Ref= useRef(null);
    const meteor2Ref= useRef(null);
    const meteor3Ref= useRef(null);

    const swiperRef = useRef(null);

    var onGoingAnim = [];


    function moveMonster1(){
        onGoingAnim.push(anime({
            targets: monster1Ref.current,
            keyframes: [
              {translateX: monster1Ref.current.getAttribute("data-center-translate-X"),translateY:monster1Ref.current.getAttribute("data-center-translate-Y"),duration:500,delay:anime.random(0,2000)},
              {translateX: monster1Ref.current.getAttribute("data-end-translate-X"),translateY: monster1Ref.current.getAttribute("data-end-translate-Y"),duration:500}
            ],
            begin: function(anim) {
                var maxTranslateX = monsterContainerRef.current.clientWidth;

                var currentTranslateX =monster1Ref.current.getAttribute("data-end-translate-X");
                if(currentTranslateX ===null){
                    currentTranslateX =0;
                }else{
                    currentTranslateX = parseInt(currentTranslateX);
                }

                var direction = anime.random(0,1) === 0?anime.random(-20,-40):anime.random(20,40);
                var withJump = anime.random(0,1)===0?true:false;

                var finalLoc = Math.min(Math.max(parseInt(currentTranslateX) + parseInt(direction), 0), maxTranslateX);


                monster1Ref.current.setAttribute("data-center-translate-X",(currentTranslateX +direction/2));
                monster1Ref.current.setAttribute("data-center-translate-Y",withJump?-10:0);
                monster1Ref.current.setAttribute("data-end-translate-X", finalLoc);
                monster1Ref.current.setAttribute("data-end-translate-Y", 0);
            },
            complete: function(anim){
                if(anim.completed){
                    moveMonster1();
                }
            },
            direction: "normal",
            easing: 'linear',
            loop: false,
        }));
    }

    function moveMonster2(){
        onGoingAnim.push(anime({
            targets: monster2Ref.current,
            keyframes: [
              {translateX: monster2Ref.current.getAttribute("data-center-translate-X"),translateY:monster2Ref.current.getAttribute("data-center-translate-Y"),duration:500, delay:anime.random(0,2000)},
              {translateX: monster2Ref.current.getAttribute("data-end-translate-X"),translateY: monster2Ref.current.getAttribute("data-end-translate-Y"),duration:500}
            ],
            begin: function(anim) {
                var maxTranslateX = monsterContainerRef.current.clientWidth;

                var currentTranslateX =monster2Ref.current.getAttribute("data-end-translate-X");
                if(currentTranslateX ===null){
                    currentTranslateX =0;
                }else{
                    currentTranslateX = parseInt(currentTranslateX);
                }

                var direction = anime.random(0,1) === 0?anime.random(-20,-40):anime.random(20,40);
                var withJump = anime.random(0,1)===0?true:false;

                var finalLoc = Math.min(Math.max(parseInt(currentTranslateX) + parseInt(direction), 0), maxTranslateX);

                monster2Ref.current.setAttribute("data-center-translate-X",(currentTranslateX +direction/2));
                monster2Ref.current.setAttribute("data-center-translate-Y",withJump?-10:0);
                monster2Ref.current.setAttribute("data-end-translate-X", finalLoc);
                monster2Ref.current.setAttribute("data-end-translate-Y", 0);
            },
            complete: function(anim){
                if(anim.completed){
                    moveMonster2();
                }
            },
            direction: "normal",
            easing: 'linear',
            loop: false,
        }));
    }

    function meteorAnim(){
        var widthToGo = projectDetailContentContainerRef.current.clientWidth;
        var heightToGo = projectDetailContentContainerRef.current.clientWidth;

        anime.set([meteor1Ref.current,meteor2Ref.current,meteor3Ref.current], {
            translateX: function() { return -1*widthToGo*1.5},
            translateY: function() { return -1*heightToGo*1.5 },
            rotate: function() { return 45 },
            opacity: function() { return 1},
        });

        onGoingAnim.push(anime({
            targets: [meteor1Ref.current,meteor2Ref.current,meteor3Ref.current],
            translateX: widthToGo,
            translateY: heightToGo, 
            delay: anime.stagger(180),
            direction: "normal",
            easing: 'easeInOutQuad',
            duration: (widthToGo+heightToGo)*3,
            loop: true
        }));
    }

    function playShowAnimation(){
        var width = projectDetailContainerRef.current.clientWidth;

        anime.set(projectDetailContainerRef.current, {
            translateX: function() { return -1*width*2 }
        });

        anime({ 
            targets: projectDetailContainerRef.current,
            translateX: 0,
            translateZ: 0.01,
            easing: 'easeInOutSine',
            duration: 2000,
            direction: 'normal',
            loop: false,
            complete: function(anim){
                moveMonster1();
                moveMonster2();
                meteorAnim();

            }
        });
    }

    function playHideAnimation(){
        var width = projectDetailContainerRef.current.clientWidth;

        anime.set(projectDetailContainerRef.current, {
            translateX: function() { return 0 }
        });

        anime({ 
            targets: projectDetailContainerRef.current,
            translateX: width*2,
            easing: 'easeInOutSine',
            duration: 2000,
            direction: 'normal',
            loop: false,
            complete: function(anim){
                props.closePopUp();
            }
        });
    }

    useEffect(()=>{
        playShowAnimation();



        swiperRef.current.update();

        projectImageContainerRef.current.style.width = `${projectImageContainerRef.current.clientHeight}px`;

        return () => {
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running[index].pause();
                    anime.running[index].remove();
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    return (
        <div>
            <div className="popUpComponentDarkFilter" />
            <div ref={projectDetailContainerRef} className="projectDetailPopUpContainer">

                <div className="rocketMonsterContainer">
                    <div ref={monsterContainerRef} className="monsterContainer">
                        <img ref={monster1Ref} alt="monster" src={redMonsterIcon} className="monsterIcon"/>
                        <img ref={monster2Ref} alt="monster" src={redMonsterIcon} className="monsterIcon"/>
                    </div>
                    <img alt="rocket" src={rocketIcon} className="rocketIcon"/>
                </div>

                <div ref={projectDetailContentContainerRef} className="projectDetailContentContainer">
                    <img ref={meteor1Ref} alt="meteor" src={meteorIcon} style={{left:15}}className="meteorIcon"/>
                    <img ref={meteor2Ref} alt="meteor" src={meteorIcon} style={{top:'10%',left:'5%'}} className="meteorIcon"/>
                    <img ref={meteor3Ref} alt="meteor" src={meteorIcon} style={{top:'20%',left:'10%'}} className="meteorIcon"/>


                    <div className="closeButton" onClick={()=>{playHideAnimation()}}>
                        <div className="closeButtonlineOne" />
                        <div className="closeButtonlineTwo" />
                    </div>

                    <div ref={projectImageContainerRef} className="projectImageContainer">
                        <img className="projectImageInnerContainer"alt="cirleImageFrame" src={CircleImageFrame} />
                        <img className="projectImageInnerContainer" alt="project main images" src={props.showingProject.projectMainImageUrl}style={{top:"20%",left:"20%",right:"20%",bottom:"10%"}}/>
                    </div>

                    <div className="projectDownloadButtonContainer">
                        {props.showingProject.githubLink !== "" && <a href={props.showingProject.githubLink}  target="_blank" rel="noopener noreferrer"><img className="projectDownloadIcon"alt="github" src={gitHubIcon} /></a>}
                        {props.showingProject.projectReport !== "" && <a href={props.showingProject.projectReportUrl} target="_blank" rel="noopener noreferrer"><img className="projectDownloadIcon"alt="github" src={downloadReportIcon} /></a>}
                    </div>

                    <div className="projectDetailTitle">{props.showingProject.projectTitle}</div>
                    <hr className="projectDetailLine"/>

                    <div style={{width:'90%',flex:'1',marginLeft:'5%',marginBottom:"10px"}}>
                        <table width="100%" height="100%" className="projectDetailTable">
                            <colgroup>
                                <col width="50%"/>
                                <col width="50%"/>
                            </colgroup>
                            <tr ><td className="projectDetailHeaderText tdHeight" colSpan={3}>Project Description</td></tr>
                            <tr>
                                <td className="projectDetailNormalText" style={{verticalAlign:"top",textAlign:"justify"}}colSpan={2} >
                                    {props.showingProject.projectDesc}
                                </td>
                            </tr>
                            <tr >
                                <td className="projectDetailHeaderText tdHeight">
                                    Project Type: 
                                </td>
                                <td className="projectDetailHeaderText tdHeight">
                                    Duration: 
                                </td>
                            </tr>
                            <tr >
                                <td className="projectDetailNormalText" height="10px"style={{verticalAlign:"top",paddingRight:10}} >
                                    {props.showingProject.projectType}
                                </td>
                                <td className="projectDetailNormalText" style={{verticalAlign:"top",paddingRight:10}}>
                                    {`${props.showingProject.projectStartDate.getDate()} ${props.showingProject.projectStartDate.toLocaleString('default', { month: 'long' })} ${props.showingProject.projectStartDate.getFullYear()} to ${props.showingProject.projectEndDate.getDate()} ${props.showingProject.projectEndDate.toLocaleString('default', { month: 'long' })} ${props.showingProject.projectEndDate.getFullYear()}`}
                                </td>
                            </tr>
                            <tr>
                                <td className="tdHeight"colspan={2}>
                                    <table width="100%">
                                        <tr>
                                            <td width="10px" className="projectDetailHeaderText tdHeight">Technology Used:</td>
                                            <td className="tdHeight" valign="top" >
                                                {props.showingProject.technologyUsedIconImagesUrl.map((url)=>(
                                                    <img key={url} alt="TechnologyIcon" src ={url} className="projectDetailTechnologyUsedIcon" />
                                                ))}
                                            </td>
                                        </tr>
                                    </table>
                                </td>
                                
                            </tr>
                            <tr><td className="projectDetailHeaderText tdHeight" colSpan={3}>Project Image</td></tr>
                        </table>
                    </div>

                    <div className="imageSliderContainer">
                        <Swiper tag="section" wrapperTag='ul' 
                            onSwiper={(swiper)=>{swiperRef.current = swiper}}
                            spaceBetween={10}
                            slidesPerView={2}
                            breakpoints={{
                                // when window width is >= 768px
                                768: {
                                    spaceBetween:50,
                                    slidesPerView: 6
                                }
                            }}
                            navigation pagination>

                            
                            {props.showingProject.projectVideoUrl!=="" && <SwiperSlide>
                                <a href={props.showingProject.projectVideoUrl} target="_blank" rel="noopener noreferrer" style={{width:"100%", height:"100%"}}>
                                    <div style={{width:"100%", height:"100%"}}>
                                        <iframe title="Video" width="100%" height="100%"style={{pointerEvents:"none"}} src={props.showingProject.projectVideoUrl}/>
                                    </div>
                                </a>
                            </SwiperSlide>}

                            {props.showingProject.projectImagesExampleUrl.map((url) =>(
                                <SwiperSlide>
                                    <Zoom>
                                        <img key={url} width="100%" height="100%" alt='test' style={{margin:"0 auto",listStyle:'none'}} src={url}/>
                                    </Zoom>
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ProjectDetailPopUpComponent;
