import React,{useEffect,useRef} from 'react';
import AboutPage from './components/AboutPage';
import EducationPage from './components/EducationPage';
import ExperiencePage from './components/ExperiencePage';
import ProjectPage from './components/ProjectPage';
import SkillsPage from './components/SkillsPage';
import ContactPage from './components/ContactPage';
import PageIndicatorComponent from './components/PageIndicatorComponent';

import NavbarComponent from './components/NavbarComponent';
import GalaxyStarAnimateBackgroundComponent from './components/GalaxyStarAnimateBackgroundComponent';
import SlideDownForMoreComponent from './components/SlideDownForMoreComponent';
import CopyRightNoticeComponent from './components/CopyRightNoticeComponent';

import $ from 'jquery';
import pagesEnum from './enums/pageEnums';
import {useSelector,useDispatch} from 'react-redux';
import {setPage} from './actions';
import { Swipeable } from 'react-swipeable'
import GalaxyParticleFollowMouseComponent from './components/GalaxyParticleFollowMouseComponent';




function MainContainer(props) {
  //getting data and dispatcher from redux
  const currentPage = useSelector(state=> state.currentPage);
  const isScrollable = useSelector(state=> state.isScrollable);
  const dispatch = useDispatch();

  //reference variable will survive even rerendered
  const mainContainerRef = useRef(null);
  const allowed = useRef(true);


  const canSwipe = useRef(true);
  const refreshAllowAfterUserSwipe = useRef(true);



  const pagesSequence = [pagesEnum.ABOUT,pagesEnum.EDUCATION,pagesEnum.EXPERIENCE,pagesEnum.PROJECT,pagesEnum.SKILLS,pagesEnum.CONTACT];

  
  function handleWheel (event){
    if(isScrollable){
      allowed.current = false;
      var isScrollingUp = event.deltaY < 0;
      //change the page state
      //handleSwipe will modify currentPage state in redux so it cause rerender of main container
      handleSwipe(isScrollingUp,true);
      setTimeout(()=>
      {
        allowed.current = true;
      },1000);
    } 
  };


  function handleSwipe(isSwipeUp){
    var currentPageIndex = pagesSequence.indexOf(currentPage);
    
    //if is scrollingup means we want to to previous page
    var pagesIndexToSwitch = isSwipeUp?currentPageIndex-1: currentPageIndex+1;

    changeToPage(pagesSequence[pagesIndexToSwitch]);
    
  }

  function changeToPage(pageToGo){
    var pagesIndexToSwitch = pagesSequence.indexOf(pageToGo);


    //if pagesIndexToSwitch not out of range
    var isScrolling = $(mainContainerRef.current).is(':animated');
    if(pagesIndexToSwitch >=0 && pagesIndexToSwitch < pagesSequence.length && isScrolling===false){
      var containerHeight = document.getElementById('mainContainer').clientHeight;

      //we count the scroll top based on the sequence
      var scrollTopValueToGo = pagesIndexToSwitch * containerHeight;
    
      
      //change current page in redux store
      dispatch(setPage(pagesSequence[pagesIndexToSwitch]));

      var jqueryObj = $(mainContainerRef.current);
        jqueryObj.animate({scrollTop:scrollTopValueToGo},800,function() {
      });
    }
  }

  function allowSwipe(isAllow,oneTime){
    canSwipe.current = isAllow;
    refreshAllowAfterUserSwipe.current = oneTime;
  }

  function checkCanSwipe(){
    if(canSwipe.current){
      return true;
    }else{
      if(refreshAllowAfterUserSwipe.current === true){
        canSwipe.current = true;
      }

      return false;
    }
  }

  useEffect(()=>{
    
    //add on wheel listener
    window.addEventListener('wheel', handleWheel);

    return () => {
      //remove listener when unmount
      window.removeEventListener('wheel', handleWheel)
    };
  });



 
  return (
    <Swipeable onSwipedUp={() => {if(checkCanSwipe()){handleSwipe(false)}}} onSwipedDown={() => {if(checkCanSwipe()){handleSwipe(true)}}} {...{trackMouse: true}} >
      <div ref={mainContainerRef} id="mainContainer" >
          <GalaxyParticleFollowMouseComponent />
          <NavbarComponent  />

          {currentPage !== pagesEnum.CONTACT && <SlideDownForMoreComponent/>}
          <CopyRightNoticeComponent/>
          <GalaxyStarAnimateBackgroundComponent />
          <PageIndicatorComponent changePageCallback={changeToPage} currentPage={currentPage} />
          {/* <div {...handlers} style={{zIndex:10,position:'fixed',left:0,right:0,bottom:0,top:0}}/> */}

          <AboutPage shouldPlayAnim ={currentPage===pagesEnum.ABOUT} />
          <EducationPage shouldPlayAnim ={currentPage===pagesEnum.EDUCATION} allowSwipe={allowSwipe} />
          <ExperiencePage shouldPlayAnim ={currentPage===pagesEnum.EXPERIENCE} allowSwipe={allowSwipe} />
          <ProjectPage shouldPlayAnim = {currentPage===pagesEnum.PROJECT} allowSwipe={allowSwipe}/>
          <SkillsPage shouldPlayAnim = {currentPage===pagesEnum.SKILLS} allowSwipe={allowSwipe}/>
          <ContactPage shouldPlayAnim = {currentPage===pagesEnum.CONTACT}/>

      </div>
    </Swipeable>
  );
}

export default MainContainer;
