import React,{useRef,useEffect} from 'react';
import anime from 'animejs/lib/anime.es.js';

const circleIcon = require('../images/circle.svg');


function TitleTextComponent(props) {
    const titleTextRef = useRef(null);
    const titleLineRef = useRef(null);
    const lineEndCircleSvgRef = useRef(null);

    var onGoingAnim = [];

    
    function playTitleAnimation(){
        var lineWidth = titleLineRef.current.clientWidth;

        anime.remove(titleLineRef.current);
        anime.remove(titleTextRef.current);
        anime.remove(lineEndCircleSvgRef.current);


        anime.set(titleLineRef.current, {
            translateX: function() { return -1*lineWidth*1.5 },
        });
        anime.set(lineEndCircleSvgRef.current, {
            scale: function() { return 0 },
        });


        var titleAnimTimeLine = anime.timeline({ 
            direction: 'normal',
            loop: false,
            complete: function(anim){
                if(anim.completed){
                    props.animDoneCallback();
                }   
            }
        });

        onGoingAnim.push(titleAnimTimeLine);
        
        
        
        //Text Animation
        var textWrapper = titleTextRef.current;
        textWrapper.style.opacity = 1;
        var charIterator = props.titleText[Symbol.iterator]();
        
        textWrapper.innerHTML="";
        var spanList = [];
        for (let char of charIterator) {
            var node = document.createElement("SPAN"); 
            node.innerHTML = char!==' '?char:'&nbsp;';
            node.classList.add('letter');
            spanList.push(node);

            textWrapper.appendChild(node);
        }
     
        // textWrapper.innerHTML = textWrapper.textContent.replace(/\S/g, "<span class='letter'>$&</span>");

        titleAnimTimeLine
        .add({
            targets: spanList,
            scale: [3,1],
            opacity: [0,1],
            translateZ: 0,
            easing: "easeOutExpo",
            duration: 1200,
            delay: (el, i) => 130*i
        });

        //Line Animation
        titleAnimTimeLine
        .add({
            targets: titleLineRef.current,
            translateX: 0,
            easing: 'easeInOutCirc',
            duration: 3000
        },"-=1800");

        //Line End Circle Animation
        titleAnimTimeLine
        .add({
            targets: lineEndCircleSvgRef.current,
            scale: 1,
            easing: 'easeInOutElastic', 
            duration: 1000
        },'-=500');
    }

    //hook to perform animation once UI rendered to the screen
    useEffect(() => {
        if(props.shouldPlayAnim){
            playTitleAnimation();
        }

        return () =>{
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[props.shouldPlayAnim]);


    return (
        <div className="titleTextContainer">
            <table style={{borderSpacing:0,borderCollapse:'seperate',overflow:'hidden'}}>
                <tbody>
                    <tr>
                        <td ref={titleTextRef} style={{opacity: props.shouldPlayAnim?0:1}}className="titleTextStyle" colSpan={2}>
                            {props.titleText}
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <div ref={titleLineRef} id="titleLine" className="titleTextLineStyle"/>
                        </td>
                        <td valign="middle">
                            <img ref={lineEndCircleSvgRef} alt="lineEndCircle" src={circleIcon} className="lineEndCircleSvg"  />
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}

export default TitleTextComponent;
