import React,{useEffect, useRef} from 'react';
import TitleTextComponent from './TitleTextComponent';
import anime from 'animejs/lib/anime.es.js';
import {useSelector} from 'react-redux';


const ufoIcon = require('../images/ContactPage/IC_Ufo.svg');
const ufoRayIcon = require('../images/ContactPage/IC_Ufo_Ray.png');
const smallStarIcon = require('../images/ContactPage/IC_Star_Small.svg');
const gitHubIcon = require('../images/IC_Github.svg');
const linkedInIcon = require('../images/IC_LinkedIn.svg');
const resumeIcon = require('../images/IC_Resume.svg');



const ContactPage = (props) => {
    //redux selector
    const contactDetail = useSelector(state=>state.contact);


    const ufoIconRef=useRef(null);
    const ufoRayRef=useRef(null);
    const ufoRayContainerRef=useRef(null);
    const contactDetailRef=useRef(null);
    const pageContainerRef= useRef(null);

    var onGoingAnim = [];

    function playContactAnimation(){
        var width = ufoIconRef.current.clientWidth;
        ufoRayRef.current.style.display="block";
        ufoRayContainerRef.current.style.display = "block";

        anime.set(ufoIconRef.current, {
            translateX: function() { return -1*width*2 },
            translateY: function() { return -10 },
        });

        var floatingUfo = anime({ 
            targets: ufoIconRef.current,
            translateY: 10,
            easing: 'linear',
            duration: 2000,
            direction: 'alternate',
            loop:true
        });

        

        var revealContactAnimTimeline = anime.timeline({
            easing: 'easeOutExpo',
            loop: false
        });
        onGoingAnim.push(revealContactAnimTimeline);

        revealContactAnimTimeline.add({
            targets: ufoIconRef.current,
            translateX: 0,
            easing: 'easeInOutQuad',
            duration: 1000,
            direction: 'normal',
            loop:false,
            complete: function(anim){
                anime.remove(floatingUfo);

                anime({ 
                    targets: ufoIconRef.current,
                    translateY: 10,
                    easing: 'linear',
                    duration: 2000,
                    direction: 'alternate',
                    loop:true
                });
            }
        });

    
        var rayHeight = ufoRayRef.current.clientHeight;
        anime.set(ufoRayRef.current, {
            translateY: function() { return -1*rayHeight },
            scaleX: function() { return 0 },
            opacity:  function() { return 1 }
        });

        revealContactAnimTimeline.add({
            targets: ufoRayRef.current,
            scaleX: 1,
            translateY: 0,
            easing: 'easeInOutQuad',
            duration: 3000,
            direction: 'normal',
            complete: function(anim){
                ufoRayRef.current.style.display="none";
                ufoRayContainerRef.current.style.display = "none";
                // setTimeout(,1000);
            }

        },'+=10')

        anime.set(contactDetailRef.current, {
            opacity:  function() { return 0 }
        });

        revealContactAnimTimeline.add({
            targets: contactDetailRef.current,
            opacity: 1,
            easing: 'linear',
            duration: 2000,
            direction: 'normal',

        },'+=300');
    }

    function spawnStar(){
        const stars=[];
     
        for (var x=0;x<20;x++) {
            stars.push(<img key={x} alt="star" src ={smallStarIcon} className="smallStar" style={{top: `${anime.random(0,100)}%`,left: `${anime.random(0,100)}%`}} />)
        }

        return stars;
    }

    useEffect(()=>{
        if(props.shouldPlayAnim){   
            playContactAnimation();

            anime.set(".smallStar", {
                scale: function() { return 0 }
            });
    
            onGoingAnim.push(anime({
                targets: ".smallStar",
                scale: 1,
                duration: 1000,
                easing: 'linear',
                direction: "alternate",
                loop:true
            }));
        }

        return () =>{
            //clear all running anim
            onGoingAnim.forEach((anim)=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });     
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });


    return (
        <div ref={pageContainerRef} className="pageContainer">
            {spawnStar()}
            <img ref={ufoIconRef} src={ufoIcon} alt="ufoIcon" className="ufoIcon"/>
            <div ref={ufoRayContainerRef} className="ufoRayContainer">
                <img ref={ufoRayRef} src={ufoRayIcon} alt="ufoRayIcon" className="ufoRayIcon" />
            </div>

            <div ref={contactDetailRef} className="contactDetailsContainer">
                <TitleTextComponent shouldPlayAnim={false} titleText="Contact"/>

                <table style={{width:"100%",height:"100%",marginTop: '8%'}}>
                    <tbody>
                        <tr>
                            <td className="contactDetailTitle">Email:</td>
                            <td>{contactDetail.email}</td>
                        </tr>
                        <tr>
                            <td className="contactDetailTitle">Links:</td>
                            <td>
                                <a href={contactDetail.githubUrl}><img className="contactLinkIcon" alt="GithubIcon" src={gitHubIcon}/></a>
                                <a href={contactDetail.linkedInUrl}><img className="contactLinkIcon" alt="LinkedInIcon" src={linkedInIcon}/></a>
                                {contactDetail.resumeUrl && <a href={contactDetail.resumeUrl} target="_blank" rel="noopener noreferrer"><img className="contactLinkIcon" alt="ResumeIcon" src={resumeIcon}/></a>}
                            </td>
                        </tr>
                    </tbody>

                </table>
            </div>
            
        </div>
    );
}

export default ContactPage;
