import React,{useState,useEffect,useContext,useRef} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import CustomFileUpload from "./formikCustomComponent/CustomFileUpload"
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'




const SkillsTab = (props) => {
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    // const [username, setUsername] = useState("");
    const [editingSkill, setEditingSkill] = useState("");

    const skillList = useSelector(state=>state.skillList);

    const skillSelectRef = useRef(null);


    //yup validation custom function
    function checkIfFilesAreTooBigArray(files){
        let valid = true;
        if (files) {
            for(var file of files){
                const size = file.size / 1024 / 1024;
                if (size > 10) {
                    valid = false;
                }
            };
        }
        return valid;
    }

    function checkIsImageArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['image/svg+xml', 'image/jpeg', 'image/png'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
    
    //yup validation Schema
    const projectValidationSchema = Yup.object().shape({
        skillName: Yup.string()
            .min(3, 'Skill Name must have at least 3 character!')
            .max(50, 'Skill Name must cannot exceed 50 character!')
            .required('Required'),
        skillDesc: Yup.string()
            .min(50, 'Skill Desc must have at least 50 character!')
            .max(500, 'Skill Desc cannot exceed 500 character!')
            .required('Required'),
        skillLevel: Yup.number()
            .required("Required")
            .min(1,"Skill Level Must be more than or equal to 1")
            .max(6,"Skill Level Must be less than or equal to 6"),
        skillImage: Yup.mixed()
            .nullable()
            .test('at-least-one-file',"Must Select Image", files=>{return files.length>0|| editingSkill !==""?true:false})
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-image-file',
            'File must be image format ex.(png,jpg,svg)',
            checkIsImageArray
        ),
      });

    useEffect(()=>{
        if(editingSkill !== ""){
             //update editting skill
             skillList.forEach((skill)=>{if(skill.id === editingSkill.id){setEditingSkill(skill)}});
        }

        return () =>{
            
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[skillList]);

    const deleteFromDatabase = ()=>{
        //show loading dialog
        Swal.fire({
            title: 'Uploading To Firebase',
            html: 'We are processing your request.',
            allowEscapeKey: false,
            allowOutsideClick: false,
            timerProgressBar: true,
            willOpen: () => {
                Swal.showLoading()
            }
        });

        firebaseContext.deleteSkillToFirebase(editingSkill.id,(success)=>{
            Swal.close();
            if(success){
                Swal.fire({
                    icon: 'success',
                    title: 'Successfully Deleted to Firebase',
                    text: 'Deleted the Skill in firebase'
                });

                setEditingSkill("");
            }else{
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
            }
        });
    }

    const findSkillWithId = (id)=>{
        var foundSkill = "";
        skillList.map((skill)=>{  
            if(skill.id === id){
                foundSkill = skill;
            }
            return null;
        });
        return foundSkill;
    }

    return (
        <div className="container-fluid" style={{marginTop: 20,paddingBottom: 200}}>
            <Formik
            initialValues={{
                skillName: editingSkill ===""?'':editingSkill.skillName,
                skillDesc: editingSkill ===""?'':editingSkill.skillDesc,
                skillImage: [],
                skillLevel: editingSkill ===""?'':editingSkill.skillLevel
            }}
            validationSchema={projectValidationSchema}
            onSubmit={(values,{ resetForm }) => {
          
                //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                });

                if(editingSkill === ""){
                    firebaseContext.addSkillToFirebase(values.skillName,values.skillDesc,values.skillLevel,values.skillImage,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Successfully added the Skill to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }else{
                    firebaseContext.editSkillToFirebase(editingSkill.id,values.skillName,values.skillDesc,values.skillLevel,values.skillImage,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Updated the Skill detail to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }

            }}
            enableReinitialize>
                {({ errors, touched,values,setFieldValue,setFieldTouched }) => (
                    <Form>
                         <div className="form-group">
                            <label >List of Skill <input type="button" value="Add New" onClick={()=>{setEditingSkill("")}}/></label>
                            <select ref={skillSelectRef} onChange={()=>{setEditingSkill(findSkillWithId(skillSelectRef.current.value))}} multiple className="form-control" >
                                {
                                    skillList.map((skill)=>(
                                        <option key={skill.id} value={skill.id} >{skill.skillName}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="input-title" ><u>{editingSkill ===""?'Add Skill':`Editing Skill: ${editingSkill.id}`}</u></div>
                        
                        <div className="input-title" >Skill Name</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='skillName'  className="form_input inputStyle" placeholder="Skill Name"/>
                        </div>
                        {touched.skillName && <span className="errorTextStyle" >{errors.skillName}</span>}

                        <div className="input-title">Skill Desc</div>
                        <Field name='skillDesc' component="textarea" className="form-control input-text-area-style mt-10" style={{marginTop: 20}} rows="5" id="comment" />
                        {touched.skillDesc && <span className="errorTextStyle" >{errors.skillDesc}</span>}


                        <div className="input-title">Skill Level</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='skillLevel'  className="form_input inputStyle" placeholder="Skill Level"/>
                        </div>
                        {touched.skillLevel && <span className="errorTextStyle" >{errors.skillLevel}</span>}


                        <div className="input-title">Skill Image</div>
                        <div style={{marginTop: 20}}>
                            <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text">Upload</span>
                            </div>
                            <CustomFileUpload
                                name = 'skillImage'
                                isMulti={false}
                                value={values.skillImage}
                                onChange={setFieldValue}
                                error={errors.skillImage}
                                touched={touched.skillImage}
                                acceptType="image/svg+xml, image/jpeg, image/png"
                                isEditing = {editingSkill !==""}
                            />
                            </div>
                        </div>
                        {touched.skillImage && <span className="errorTextStyle" >{errors.skillImage}</span>}


                        <input type="submit" onClick={()=>{if(Object.keys(errors).length !== 0){Swal.fire({icon: 'error', title: 'Input error',text: 'Please fix all error first!'});}}}className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Edit To Firebase"/>
                        {editingSkill !== "" && <input type="button" className="btn btn-outline-secondary btn-block" onClick={()=>{deleteFromDatabase();}} value="Delete"/>}
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default SkillsTab;
