import React,{useEffect, useRef} from 'react';
import TitleTextComponent from './TitleTextComponent';
import BlackHoleComponent from './BlackHoleComponent';
import anime from 'animejs/lib/anime.es.js';
import {useSelector} from 'react-redux';



const EducationPage = (props)=> {
    const allEducationScrollableContainerRef = useRef(null);

    var onGoingAnim = [];
    const educationList = useSelector(state=>state.educationList);


    function playEducationAnimation(){
        var width = allEducationScrollableContainerRef.current.clientWidth;

        anime.set('.educationContainer', {
            translateX: function() { return -1*width }
        });
    
    
        onGoingAnim.push(anime({ 
            targets: ".educationContainer",
            translateX: 0,
            direction: 'normal',
            easing: "easeInOutQuad",
            duration: 1000,
            delay: anime.stagger(800),
            loop: false
        }));

    }

    useEffect(() =>{

        return () =>{
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });


    return (
        <div className="pageContainer">
            <div className="contentContainer">
                <TitleTextComponent key={'EducationpageTitleText'} shouldPlayAnim={props.shouldPlayAnim} titleText="Education" animDoneCallback={()=>{playEducationAnimation();}} />

                <div ref={allEducationScrollableContainerRef} className="allEducationScrollableContainer" onScroll={()=>{props.allowSwipe(false,true);}}>
                    {educationList.map((education) => (
                        <div key={education.id} style={{transform: props.shouldPlayAnim?"translateX(-100%)":"translateX(0)"}}className="educationContainer">
                            <div className="educationImageContainer">
                                <div className="educationImageInnerContainer">
                                    <img alt="institution Icon" src={education.institutionImageUrl} width="100%" height="100%"/>
                                </div>
                            </div>
                            
                            
                            <div className="educationContentContainer">
                                <div className="educationTitle">{education.educationTitle}</div>
                                <hr className="educationLine"/>
                                <div className="educationLocation">{education.educationInstitution+`, ${new Date(education.educationStartDate).toLocaleString('default', { month: 'long' })} ${new Date(education.educationStartDate).getFullYear()} to ${new Date(education.educationEndDate).toLocaleString('default', { month: 'long' })} ${new Date(education.educationEndDate).getFullYear()}`}</div>
                                <div style={{marginTop:-5}}>
                                    <div className="educationAchievementTitle">Achievement:</div>
                                    <div className="educationAchievement"> {education.achievement}</div>
                                </div>
                                <div className="educationDesc">
                                    {education.educationDesc}
                                </div>
                            </div>
                            
                        </div>
                    ))}
                </div>
            </div>
            <BlackHoleComponent shouldPlayAnim={props.shouldPlayAnim} />
        </div>
    );
}

export default EducationPage;
