import React,{useEffect,useContext,useState} from 'react';
import MainContainer from './MainContainer';
import LoadingPageComponent from './components/LoadingPageComponent';
import DashboardPage from './components/dashboard/DashboardPage.js';

import {  BrowserRouter as Router,  Switch,  Route} from "react-router-dom";
import { FirebaseContext } from './components/firebase';

import pagesEnum from './enums/pageEnums';
import {useDispatch} from 'react-redux';
import {setPage} from './actions';





function MainRouterContaienr(props) {
    //context declaration
    var firebaseContext= useContext(FirebaseContext);

    const dispatch = useDispatch();



    //state declaration
    const [isLoadFinish, setIsLoadFinish] = useState(false);
    function fetchAllDatafromFirebase(){

      // setIsLoadFinish(true);
      //     dispatch(setPage(pagesEnum.ABOUT));
      var fetchDataPromises = [];


      // fetch About
      fetchDataPromises.push(firebaseContext.refreshAboutDetailToRedux())


      //fetch Contact
      fetchDataPromises.push(firebaseContext.refreshContactDetailToRedux())

      //fetch Education
      fetchDataPromises.push(firebaseContext.refreshEducationListToRedux())


      //fetch Skilllist and project list
      fetchDataPromises.push(new Promise((resolve,reject)=>{
        firebaseContext.refreshSkillListToRedux().then((result)=>{
          firebaseContext.refreshProjectListToRedux().then((result)=>{
            resolve()
          })
        })
      }))


      //fetch Experiencelist
      fetchDataPromises.push(firebaseContext.refreshExperienceListToRedux())


      Promise.all(fetchDataPromises).then((result)=>{
        setIsLoadFinish(true);
        dispatch(setPage(pagesEnum.ABOUT));
      })
    }

  


  
  useEffect(()=>{
  
    fetchAllDatafromFirebase();

    return () => {
      
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  },[]);



 
  return (
    <Router>
        {!isLoadFinish && <LoadingPageComponent /> }

        <Switch>
            <Route path="/dashboard">
                <DashboardPage />
            </Route>    
            <Route path="/">
                <MainContainer />
            </Route>
        </Switch>
    </Router>
  );
}

export default MainRouterContaienr;
