  editEducationToFirebase(educationId,educationTitle,educationDesc,educationInstitution,achievement,institutionImage,educationStartDate,educationEndDate,callback){
    var classRef = this;


    //find education reference in redux based on the id
    var educationList = classRef.reduxStore.getState().educationList;
    var educationRef = null;
    educationList.forEach((education)=>{if(education.id === educationId)educationRef = education});

    

    //first upload document
    var newData = {
      educationTitle: educationTitle,
      educationDesc: educationDesc,
      educationInstitution: educationInstitution,
      educationStartDate: educationStartDate,
      educationEndDate: educationEndDate,
      achievement: achievement
    }

    //if got new image we need to remove it from storage
    if(institutionImage.length >0){
      newData.institutionImage="";

      //remove old image
      var myImageRef = classRef.firebaseStorage.child(educationRef.institutionImage);
      myImageRef.delete().then(function(){

      });
    }

    classRef.firestore.collection("Education").doc(educationId).update(newData).then(function(docRef) {

      if(institutionImage.length <=0){
        classRef.refreshEducationListToRedux((success,message)=>{callback(success);});

      }else{
        //upload education institution Image
        var imageToUpload = institutionImage[0]// use the Blob or File API

        var mainImageUrl =`Education/${educationId}/${classRef.uuidv4()}`
        var mainImageRef = classRef.firebaseStorage.child(mainImageUrl);
        mainImageRef.put(imageToUpload).then(function(snapshot) {
          classRef.firestore.collection("Education").doc(educationId).update({institutionImage:snapshot.ref.fullPath}).then(function() {
              classRef.refreshEducationListToRedux((success,message)=>{callback(success);});
          })
          .catch(function(error) {
              // The document probably doesn't exist.
              callback(false);
              console.error("Error updating document: ", error);
          });
        });
      }
    })
    .catch(function(error) {
        callback(false);
        console.error("Error writing document: ", error);
    });
  }

