import React,{useEffect, useState,useContext} from 'react';
import LoginPage from './LoginPage';
import AboutTab from './AboutTab';
import ContactTab from './ContactTab';
import ProjectTab from './ProjectTab';
import SkillsTab from './SkillsTab';
import EducationTab from './EducationTab';
import ExperienceTab from './ExperienceTab';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'








const DashboardPage = (props) => {
    var firebaseContext= useContext(FirebaseContext);


    //ref declaration




    //state declaration
    const [currentTab, setCurrentTab] = useState('about');
    const [isLoggedIn, setIsLoggedIn] = useState(false);


    useEffect(() => {
        const unsubscribe = firebaseContext.firebaseAuth.onAuthStateChanged((user) => { // detaching the listener
            if (user) {
                setIsLoggedIn(true);
            } else {
                if(isLoggedIn === true){
                    // Handle error.
                    Swal.fire({
                        icon: 'error',
                        title: 'You are Logged out!',
                        text: 'Please login again for access to the dashboard.'
                    });
                    setIsLoggedIn(false); 
                }
                
            }
        });
        return () => unsubscribe();  
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);


    return (
        <div style={{width:'100vw',height:'100vh',background:'white'}}>
            <div className="container">
                {!isLoggedIn && <LoginPage setIsLoggedIn={setIsLoggedIn}/>}
                
                {isLoggedIn && <div id="dashboard">
                    <h1 style={{textAlign: 'center'}}>Dashboard</h1>
                    <ul className="nav nav-tabs">
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}}className={currentTab==='about'?"nav-link active":"nav-link"} onClick={(e)=>setCurrentTab('about')}>About</span>
                        </li>
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}} className={currentTab==='education'?"nav-link active":"nav-link"} onClick={(e)=>setCurrentTab('education')}>Education</span>
                        </li>
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}} className={currentTab==='experience'?"nav-link active":"nav-link"} onClick={(e)=>setCurrentTab('experience')}>Experience</span>
                        </li>
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}} className={currentTab==='project'?"nav-link active":"nav-link"} onClick={(e)=>setCurrentTab('project')}>Project</span>
                        </li>
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}} className={currentTab==='skill'?"nav-link active":"nav-link"} onClick={(e)=>setCurrentTab('skill')}>Skill</span>
                        </li>
                        <li className="nav-item">
                            <span style={{cursor:'pointer'}} className={currentTab==='contact'?"nav-link active":"nav-link"}  onClick={(e)=>setCurrentTab('contact')} >Contact</span>
                        </li>
                    </ul>
                    
                    <div className="dashboard-form">

                        {currentTab==="about" && <AboutTab />}
                        {currentTab==="contact" && <ContactTab />}
                        {currentTab==="project" && <ProjectTab />}
                        {currentTab==="skill" && <SkillsTab />}
                        {currentTab==="education" && <EducationTab />}
                        {currentTab==="experience" && <ExperienceTab />}




                    </div>
                </div>}
            </div>
        </div>
    );
}

export default DashboardPage;
