import React,{useRef,useEffect} from 'react';
import * as galaxyParticleLib from '../galaxyParticle/galaxyParticle.js';
import Swal from 'sweetalert2'




const GalaxyParticleFollowMouseComponent = (props) => {
    

    //state declaration
    const galaxyParticleContainer = useRef(null);

    const mouse = useRef([0,0]);
    const center = useRef([0,0]);
    const hover = useRef(false);
    const particles = useRef([]);

    const canvasRef = useRef(null)



    useEffect(()=>{
        //function declaration
        function draw() {
            buffer.clearRect(0, 0, buffer.current.canvas.width, buffer.current.canvas.height);
        
            center.current.lerp(hover.current ? mouse.current : [0.5 * buffer.canvas.width, 0.5 * buffer.canvas.height], 0.05);
        
            let i;
        
            for (i = 0; i < particleCount; i++) {
                particles.current[i].draw().update();
            }
        
            window.requestAnimationFrame(draw);
        }

        function resize(e) {
            const { innerWidth, innerHeight } = window;
        
            buffer.canvas.width = ctx.canvas.width = innerWidth;
            buffer.canvas.height = ctx.canvas.height = innerHeight;
        
            center.current[0] = 0.5 * innerWidth;
            center.current[1] = 0.5 * innerHeight;
        }
        
        function mouseHandler(e) {
            const { type, clientX, clientY } = e;
        
            hover.current = type === "mousemove";
        
            mouse.current[0] = clientX;
            mouse.current[1] = clientY;
        }


        //for mobile device
        function randomMousePosition(){
            var maxWidth = galaxyParticleContainer.current.clientWidth;
            var maxHeight = galaxyParticleContainer.current.clientHeight;

            hover.current = true;
            mouse.current[0] = Math.floor(Math.random() * (maxWidth - 0) ) + 0;
            mouse.current[1] = Math.floor(Math.random() * (maxHeight - 0) ) + 0;
        }

        //start initialize galaxy particle
        const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
        const particleCount = isMobile? 20:50;
        // const {buffer,ctx} = galaxyParticleLib.createRenderingContext(galaxyParticleContainer.current.clientWidth,galaxyParticleContainer.current.clientHeight,galaxyParticleContainer.current);
        const canvas = canvasRef.current
        const ctxRef = canvas.getContext('2d')
        const buffer = ctxRef;
        const ctx = ctxRef;

        if(isMobile && navigator.userAgent.includes('Firefox')){
            Swal.fire({
                icon: 'info',
                title: 'Some Feature are not enabled for mobile firefox',
                text: 'Use Desktop Chrome for best experience. :D'
              });
        }else{
            if(navigator.userAgent.includes('Firefox')){
                Swal.fire({
                    icon: 'info',
                    title: 'Firefox does not support offscreenCanvas',
                    text: 'Use Desktop Chrome for best experience. :D'
                  });
            }
            if(isMobile){
                Swal.fire({
                    icon: 'info',
                    title: 'Some Feature are not enabled for mobile devices',
                    text: 'Use Desktop Chrome for best experience. :D'
                  });
            }

            
            
            buffer.current = ctxRef;
            ctx.current = ctxRef;

            // buffer.current = buffer;
            // ctx.current = ctx;

            //resize canvas to screen size
            resize();

            var grd = ctxRef.createLinearGradient(0, 0, buffer.current.canvas.height, buffer.current.canvas.width);
            grd.addColorStop(0, "rgba(44 ,62 ,80,0.5)");
            grd.addColorStop(1, "rgba(22 ,0 ,42,0.5)");
            ctxRef.fillStyle = grd;
            ctxRef.fillRect(0, 0, buffer.current.canvas.width, buffer.current.canvas.height);
            
            ctxRef.save();
            ctxRef.filter = "blur(5px) saturate(200%) contrast(200%)";
            ctxRef.drawImage(buffer.canvas, 0, 0);
            ctxRef.restore();
            
            ctxRef.save();
            ctxRef.globalCompositeOperation = "lighter";
            ctxRef.drawImage(buffer.canvas, 0, 0);
            ctxRef.restore();

            //create particle
            for (var i = 0; i < particleCount; i++) {
                particles.current.push(new galaxyParticleLib.Particle(buffer.current,center));
            }


            draw();
        
            var randomMousePositionInterval = null;
            window.addEventListener("resize", resize);
            if(isMobile){
                randomMousePositionInterval = setInterval(randomMousePosition,5000)
            }else{
                window.addEventListener("mousemove", mouseHandler);
                window.addEventListener("mouseout", mouseHandler);
            }
        }
        
        return ()=>{
            if(isMobile && navigator.userAgent.includes('Firefox')){
                window.removeEventListener("resize", resize);
                if(isMobile){
                    window.clearInterval(randomMousePositionInterval);
                }else{
                    window.removeEventListener("mousemove", mouseHandler);
                    window.removeEventListener("mouseout", mouseHandler);
                }
            }
        }

                
    },[]);

    

    return (
        <div ref={galaxyParticleContainer} style={{position:'fixed',top:0,left:0,right:0,bottom:0,zIndex:-1}}>
          <canvas ref={canvasRef} {...props} style={{width:"100%",height:"100%"}}/>
        </div>
    );
}

export default GalaxyParticleFollowMouseComponent;
