import React from 'react';
import {useSelector} from 'react-redux';

const gitHubIcon = require('../images/IC_Github.svg');
const linkedInIcon = require('../images/IC_LinkedIn.svg');
const resumeIcon = require('../images/IC_Resume.svg');



function NavbarComponent() {
  const contactDetail = useSelector(state=>state.contact);

  return (
    <div id="navBarContainer">
        <div className="logo-wrapper">
            Ow Yeong
        </div> 

        <div className="profile-wrapper">
            {contactDetail.resumeUrl && <a href={contactDetail.resumeUrl} target="_blank" rel="noopener noreferrer"><img className="icon" alt="ResumeIcon" src={resumeIcon}/></a>}
            <a href={contactDetail.githubUrl} ><img className="icon" alt="GithubIcon" src={gitHubIcon}/></a>
            <a href={contactDetail.linkedInUrl} ><img className="icon" alt="LinkedInIcon" src={linkedInIcon}/></a>
        </div>
    </div>
  );
}

export default NavbarComponent;
