import React,{useEffect, useRef} from 'react';
import anime from 'animejs/lib/anime.es.js';

const arrowDownImage = require('../images/IC_ArrowDown.svg');




function SlideDownForMoreComponent(props) {

    
    const scrollDownForMoreContainerRef = useRef(null);

    useEffect(() =>{

        anime.set(scrollDownForMoreContainerRef.current, {
            translateY: function() { return -10 },
        });
        

        anime({ 
            targets: scrollDownForMoreContainerRef.current,
            translateY:10,
            translatez:1,
            rotate: 0.01,
            duration:1000,
            easing: 'linear',
            direction: 'alternate',
            loop: true,
            
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    return (  
        <div ref={scrollDownForMoreContainerRef} className="scrollDownForMoreContainer">
            Swipe down for more 
            <img width='15' height='15' src={arrowDownImage} alt="arrowdown" style={{marginLeft:15,display:'inline-block'}}/>
        </div>
    );
}

export default SlideDownForMoreComponent;
