import React,{useEffect, useRef} from 'react';
import anime from 'animejs/lib/anime.es.js';
import lottie from 'lottie-web';
import pagesEnum from '../enums/pageEnums';
import {useSelector} from 'react-redux';

//About Anim
import HelloAnim from "../lottieanims/HelloAnim.json";
import CoffeeMachine from "../lottieanims/AboutPageAnim/CoffeeMachine.json";
import KualaLumpur from "../lottieanims/AboutPageAnim/KualaLumpur.json";
import Game from "../lottieanims/AboutPageAnim/Game.json";
import ILoveProgramming from "../lottieanims/AboutPageAnim/ILoveProgramming.json";

//Education Anim
import EducationAnim from "../lottieanims/EducationAnim.json";
import Assignment from "../lottieanims/EducationPageAnim/Assignment.json";
import Exam from "../lottieanims/EducationPageAnim/Exam.json";
import Discussion from "../lottieanims/EducationPageAnim/Discussion.json";
import Books from "../lottieanims/EducationPageAnim/Books.json";

//Experience Anim
import ExperienceAnim from "../lottieanims/ExperienceAnim.json";
import Coding from "../lottieanims/ExperiencePageAnim/Coding.json";
import Report from "../lottieanims/ExperiencePageAnim/Report.json";
import ShirtAndPant from "../lottieanims/ExperiencePageAnim/ShirtAndPant.json";
import Slides from "../lottieanims/ExperiencePageAnim/Slides.json";

//Project Anim
import ProjectAnim from "../lottieanims/ProjectAnim.json";
import FinalReport from "../lottieanims/ProjectPageAnim/FinalReport.json";
import GitCommand from "../lottieanims/ProjectPageAnim/GitCommand.json";
import Survey from "../lottieanims/ProjectPageAnim/Survey.json";
import Testing from "../lottieanims/ProjectPageAnim/Testing.json";



import SkillAnim from "../lottieanims/SkillAnim.json";


const blackHole = require('../images/BlackHole.svg');



function BlackHoleComponent(props) {

    const blackHoleImgRef = useRef(null);
    const blackHoleCenterLottieAnimContainerRef = useRef(null);
    const blackHoleTopLeftLottieAnimContainerRef = useRef(null);
    const blackHoleTopRightLottieAnimContainerRef = useRef(null);
    const blackHoleBottomLeftLottieAnimContainerRef = useRef(null);
    const blackHoleBottomRightLottieAnimContainerRef = useRef(null);
    const currentPage = useSelector(state=> state.currentPage);


    var onGoingAnim = [];

    function getCharacterAnimBasedOnPage(){
        var animList = [];
        switch(currentPage) {
            case pagesEnum.ABOUT:
                animList=[HelloAnim,KualaLumpur,CoffeeMachine,ILoveProgramming,Game];
                break;
            case pagesEnum.EDUCATION:
                animList= [EducationAnim,Books,Exam,Assignment,Discussion];
                break;
            case pagesEnum.EXPERIENCE:
                animList= [ExperienceAnim,Coding,ShirtAndPant,Slides,Report];
                break;
            case pagesEnum.PROJECT:
                animList= [ProjectAnim,GitCommand,Survey,Testing,FinalReport];
                break;
            case pagesEnum.SKILLS:
                animList= [SkillAnim];
                break;
            default:
                animList= [HelloAnim,KualaLumpur,CoffeeMachine,ILoveProgramming,Game];
        }
        return animList;
    }


    useEffect(() =>{
    

        if(props.shouldPlayAnim){
            anime.set([blackHoleCenterLottieAnimContainerRef.current,blackHoleTopLeftLottieAnimContainerRef.current,blackHoleTopRightLottieAnimContainerRef.current,blackHoleBottomLeftLottieAnimContainerRef.current,blackHoleBottomRightLottieAnimContainerRef.current], {
                opacity: function() { return 0 }
            });
            anime.set(blackHoleImgRef.current, {
                scale: function() { return 0 },
            });
    
            var blackHoleInitialAnimTimeline = anime.timeline({ 
                direction: 'normal',
                loop: false,
                complete: function(anim){
                    anime.set(blackHoleImgRef.current, {
                        rotate: function() { return 0 },
                    });
    
                    //infinite anime
                    onGoingAnim.push(anime({ 
                        targets: blackHoleImgRef.current,
                        translateY: 8,
                        scale: 1.05,
                        rotate: function() { return anime.random(-40, 40); },
                        easing: "linear",
                        duration: 3000,
                        direction: 'alternate',
                        loop: true
                    }));
            
                
                }
            });
            onGoingAnim.push(blackHoleInitialAnimTimeline);
            
            var lottieAnimList = [];
            //character anim
            lottieAnimList.push(lottie.loadAnimation({
                container: blackHoleCenterLottieAnimContainerRef.current, 
                renderer: 'svg',
                loop: true,
                autoplay: false,
                animationData: getCharacterAnimBasedOnPage()[0]
            }));

            //because skill page dun have corner icon
            if(getCharacterAnimBasedOnPage().length > 1){
                lottieAnimList.push(lottie.loadAnimation({
                    container: blackHoleTopLeftLottieAnimContainerRef.current, 
                    renderer: 'svg',
                    loop: true,
                    autoplay: false,
                    animationData: getCharacterAnimBasedOnPage()[1]
                }));
    
                lottieAnimList.push(lottie.loadAnimation({
                    container: blackHoleTopRightLottieAnimContainerRef.current, 
                    renderer: 'svg',
                    loop: true,
                    autoplay: false,
                    animationData: getCharacterAnimBasedOnPage()[2]
                }));
    
                lottieAnimList.push(lottie.loadAnimation({
                    container: blackHoleBottomLeftLottieAnimContainerRef.current, 
                    renderer: 'svg',
                    loop: true,
                    autoplay: false,
                    animationData: getCharacterAnimBasedOnPage()[3]
                }));
    
                lottieAnimList.push(lottie.loadAnimation({
                    container: blackHoleBottomRightLottieAnimContainerRef.current, 
                    renderer: 'svg',
                    loop: true,
                    autoplay: false,
                    animationData: getCharacterAnimBasedOnPage()[4]
                }));
            }
            


            blackHoleInitialAnimTimeline
            .add({
                targets: blackHoleImgRef.current,
                scale: 1,
                rotate: -360,
                easing: "easeInOutElastic",
                duration: 6000,
                complete: function(){


                    // float character out 
                    var width = blackHoleCenterLottieAnimContainerRef.current.clientWidth;

                    anime.set([blackHoleCenterLottieAnimContainerRef.current,blackHoleTopLeftLottieAnimContainerRef.current,blackHoleTopRightLottieAnimContainerRef.current,blackHoleBottomLeftLottieAnimContainerRef.current,blackHoleBottomRightLottieAnimContainerRef.current], {
                        translateX: function() { return width },
                        opacity: function() { return 1 },
                        translateY: function(){ return 0},
                        rotate: function(){ return 0}
                    });


                    onGoingAnim.push(anime({
                        targets: [blackHoleCenterLottieAnimContainerRef.current,blackHoleTopLeftLottieAnimContainerRef.current,blackHoleTopRightLottieAnimContainerRef.current,blackHoleBottomLeftLottieAnimContainerRef.current,blackHoleBottomRightLottieAnimContainerRef.current],
                        keyframes:[
                            {translateX: width/2,translateY: -130},
                            {translateX: 0,translateY: 0}
                        ],
                        rotate:-360,
                        opacity: 1,
                        duration: 5000,
                        loop: false,
                        easing: 'linear',
                        complete: function(anim){
                            lottieAnimList.forEach(lottieAnim=> lottieAnim.play());
                            
                            onGoingAnim.push(anime({
                                targets: [blackHoleCenterLottieAnimContainerRef.current,blackHoleTopLeftLottieAnimContainerRef.current,blackHoleTopRightLottieAnimContainerRef.current,blackHoleBottomLeftLottieAnimContainerRef.current,blackHoleBottomRightLottieAnimContainerRef.current],
                                translateY: 10,
                                duration: 1000,
                                direction: 'alternate',
                                loop: true,
                                easing: 'linear',
                            }))
                        }
                    }));

                }
            });
    
        }

        return () =>{
            if(props.shouldPlayAnim){
                lottieAnimList.forEach(lottieAnim=>lottieAnim.destroy());
            }

            //clear all running anim
            onGoingAnim.forEach((anim)=>{
                // anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });     
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    return (
        <div className="blackHoleContainer">
            <img id="blackHoleImg" ref={blackHoleImgRef} src ={blackHole} alt="blackHole" width='100%' height='100%'/>
            <div ref={blackHoleCenterLottieAnimContainerRef} className="blackHoleCenterLottieAnimContainer"/>
            <div ref={blackHoleTopLeftLottieAnimContainerRef} className="blackHoleTopLeftLottieAnimContainer" />
            <div ref={blackHoleTopRightLottieAnimContainerRef} className="blackHoleTopRightLottieAnimContainer" />
            <div ref={blackHoleBottomLeftLottieAnimContainerRef} className="blackHoleBottomLeftLottieAnimContainer" />
            <div ref={blackHoleBottomRightLottieAnimContainerRef} className="blackHoleBottomRightLottieAnimContainer" />


        </div>
    );
}

export default React.memo(BlackHoleComponent);
