import React,{useRef} from 'react';
import TitleTextComponent from './TitleTextComponent';
import BlackHoleComponent from './BlackHoleComponent';
import anime from 'animejs/lib/anime.es.js';
import {useSelector} from 'react-redux';





const AboutPage = (props) => {
    //redux selector
    const aboutDetail = useSelector(state=>state.about);

    //state declaration
    const descContainerRef = useRef(null);

    function playDescAnimation(){
        anime.remove();

        anime.set(descContainerRef.current, {
            opacity: function() { return 0 },
            translateY: function() { return 50 }
        });
    
    
        anime({ 
            targets: descContainerRef.current,
            opacity: 1,
            translateY: 0,
            direction: 'normal',
            easing: "linear",
            duration: 1000,
            loop: false
        });

    }


    return (
        <div className="pageContainer">
            <div className="contentContainer">
                <TitleTextComponent key={'AboutpageTitleText'} shouldPlayAnim={props.shouldPlayAnim}animDoneCallback={()=>{playDescAnimation()}}titleText={aboutDetail.title} />

                <div style={{opacity:props.shouldPlayAnim?0:1}} ref={descContainerRef} className="descContainer">{aboutDetail.desc}</div>
            </div>
            <BlackHoleComponent shouldPlayAnim={props.shouldPlayAnim} />
        </div>
    );
}

export default AboutPage;
