import React,{useState,useEffect,useContext,useRef} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import CustomSelect from "./formikCustomComponent/CustomSelect"
import CustomFileUpload from "./formikCustomComponent/CustomFileUpload"
import CustomDatePicker from "./formikCustomComponent/CustomDatePicker"
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'




const ProjectTab = (props) => {
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    // const [username, setUsername] = useState("");
    const [editingProject, setEditingProject] = useState("");

    const skillList = useSelector(state=>state.skillList);
    const projectList = useSelector(state=>state.projectList);

    var projectSelectRef = useRef(null);


    //yup validation custom function
    function checkIfFilesAreTooBigArray(files){
        let valid = true;
        if (files) {
            for(var file of files){
                const size = file.size / 1024 / 1024;
                if (size > 10) {
                    valid = false;
                }
            };
        }
        return valid;
    }

    function checkIsPDFArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['application/pdf'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
    
    function checkIsImageArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['image/svg+xml', 'image/jpeg', 'image/png'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
    
    //yup validation Schema
    const projectValidationSchema = Yup.object().shape({
        projectTitle: Yup.string()
            .min(10, 'Project Title must have at least 10 character!')
            .max(50, 'Project Title must cannot exceed 50 character!')
            .required('Required'),
        projectDesc: Yup.string()
            .min(50, 'Project Desc must have at least 50 character!')
            .max(500, 'Project Desc cannot exceed 500 character!')
            .required('Required'),
        projectType: Yup.string()
            .min(10, 'Project Type must have at least 10 character!')
            .max(50, 'Project Type cannot exceed 50 character!')
            .required('Required'),
        projectStartDate: Yup.string()
            .required('Required'),
        projectEndDate: Yup.string()
            .required('Required'),
        technologyUsedList: Yup.array()
            .min(1, 'Pick at least 1 tags')
            .of(
                Yup.object().shape({
                    label: Yup.string().required(),
                    value: Yup.string().required(),
                })
            ),
        projectMainImage: Yup.mixed()
            .nullable()
            .test('at-least-one-file',"Must Select Image", files=>{return files.length>0|| editingProject !==""?true:false})
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-image-file',
            'File must be image format ex.(png,jpg,svg)',
            checkIsImageArray
        ),
        projectImagesExample: Yup.mixed()
        .nullable()
        .test('at-least-one-file',"Must have at least 1 image", files=>{return files.length>0 || editingProject !==""?true:false})
        .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
        .test(
          'is-image-file',
          'File must be image format ex.(png,jpg,svg)',
          checkIsImageArray
        ),
        projectReport: Yup.mixed()
            .nullable()
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-pdf-file',
            'File must be PDF format',
            checkIsPDFArray
        ),
      });

    useEffect(()=>{
        if(editingProject !== ""){
             //update editting project
             projectList.forEach((project)=>{if(project.id === editingProject.id){setEditingProject(project)}});
        }

        return () =>{
            
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[projectList]);

    const getTechnologyOptions = () =>{
        var optionsList = [];
        
        for(var skill of skillList){
            optionsList.push({ value: skill.id, label: skill.skillName });
        }

        return optionsList;
    }

    
    const getSelectedTechnologyList = (technologyList) =>{
        var selectedList=[];
        for(var skill of skillList){
            if(technologyList.includes(skill.id)){
                selectedList.push({ value: skill.id, label: skill.skillName });
            }
        }
        return selectedList;
    }

    const deleteFromDatabase = ()=>{
        //show loading dialog
        Swal.fire({
            title: 'Uploading To Firebase',
            html: 'We are processing your request.',
            allowEscapeKey: false,
            allowOutsideClick: false,
            timerProgressBar: true,
            willOpen: () => {
                Swal.showLoading()
            }
        });

        firebaseContext.deleteProjectToFirebase(editingProject.id,(success)=>{
            Swal.close();
            if(success){
                Swal.fire({
                    icon: 'success',
                    title: 'Successfully Deleted to Firebase',
                    text: 'Deleted the project in firebase'
                });

                setEditingProject("");
            }else{
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
            }
        });
    }

    const findProjectWithProjectId = (id)=>{
        var foundProject = "";
        projectList.map((project)=>{  
            if(project.id === id){
                foundProject = project;
            }
            return null;
        });
        return foundProject;
    }
    return (
        <div className="container-fluid" style={{marginTop: 20,paddingBottom: 200}}>
            <Formik
            initialValues={{
                projectTitle: editingProject ===""?'':editingProject.projectTitle,
                projectDesc: editingProject ===""?'':editingProject.projectDesc,
                projectType: editingProject ===""?'':editingProject.projectType,
                githubLink: editingProject ===""?'':editingProject.githubLink,
                projectStartDate: editingProject ===""?'':editingProject.projectStartDate,
                projectEndDate: editingProject ===""?'':editingProject.projectEndDate,
                projectMainImage: [],
                projectImagesExample: [],
                projectVideoUrl: editingProject ===""?'':editingProject.projectVideoUrl,
                technologyUsedList: editingProject ===""?[]:getSelectedTechnologyList(editingProject.technologyUsed),
                projectReport: []
            }}
            validationSchema={projectValidationSchema}
            onSubmit={(values,{ resetForm }) => {
                var technologyListOnlyValues = [];
                values.technologyUsedList.forEach(tech=>{
                    technologyListOnlyValues.push(tech.value);
                });
                //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                });

                var filesList = [...values.projectImagesExample];
                filesList.sort((a,b)=>{
                    try{
                      var numberA= parseInt(a.name.split(".")[0]);
                      var numberB= parseInt(b.name.split(".")[0]);
                      if(numberA > numberB){
                        return 1;
                      }else{
                        return -1;
                      }
        
                    }catch(e){
                      return -1;
                    }
                  });

                if(editingProject === ""){
                    firebaseContext.addProjectToFirebase(values.projectTitle,values.projectDesc,values.projectMainImage,filesList,values.projectVideoUrl,technologyListOnlyValues,values.projectStartDate,values.projectEndDate,values.projectType,values.githubLink,values.projectReport,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Successfully added the project to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }else{
                    firebaseContext.editProjectToFirebase(editingProject.id,values.projectTitle,values.projectDesc,values.projectMainImage,filesList,values.projectVideoUrl,technologyListOnlyValues,values.projectStartDate,values.projectEndDate,values.projectType,values.githubLink,values.projectReport,(success)=>{
                        Swal.close();
                        if(success){
                            Swal.fire({
                                icon: 'success',
                                title: 'Successfully Uploaded to Firebase',
                                text: 'Updated the project detail to firebase'
                            });
                            resetForm();
                        }else{
                            Swal.fire({
                                icon: 'error',
                                title: 'Oops...',
                                text: 'Something went wrong!'
                            });
                        }
                    });
                }

            }}
            enableReinitialize>
                {({ errors, touched,values,setFieldValue,setFieldTouched }) => (
                    <Form>
                         <div className="form-group">
                            <label >List of project <input type="button" value="Add New" onClick={()=>{setEditingProject("")}}/></label>
                            <select ref={projectSelectRef} multiple className="form-control" onChange={()=>{setEditingProject(findProjectWithProjectId(projectSelectRef.current.value))}}>
                                { 
                                    projectList.map((project)=>(   
                                        <option key={project.id} value={project.id} >{project.projectTitle}</option>
                                    ))
                                }
                            </select>
                        </div>
                        <div className="input-title" ><u>{editingProject ===""?'Add Project':`Editing Project: ${editingProject.id}`}</u></div>
                        
                        <div className="input-title" >Project Title</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='projectTitle'  className="form_input inputStyle" placeholder="Project Title"/>
                        </div>
                        {touched.projectTitle && <span className="errorTextStyle" >{errors.projectTitle}</span>}


                        <div className="input-title">Project Desc</div>
                        <Field name='projectDesc' component="textarea" className="form-control input-text-area-style mt-10" style={{marginTop: 20}} rows="5" id="comment" />
                        {touched.projectDesc && <span className="errorTextStyle" >{errors.projectDesc}</span>}

                        <div className="input-title" >Project Type</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='projectType'  className="form_input inputStyle" placeholder="Project Type"/>
                        </div>
                        {touched.projectType && <span className="errorTextStyle" >{errors.projectType}</span>}

                        <div className="input-title" >Github Link</div>
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='githubLink'  className="form_input inputStyle" placeholder="Github Link"/>
                        </div>
                        {touched.githubLink && <span className="errorTextStyle" >{errors.githubLink}</span>}
                        
                        <div className="input-title">Project Start and End Date</div>
                        <div style={{width:"100%",minHeight: 40}}>
                            <CustomDatePicker type="text" name='projectStartDate'  value={values.projectStartDate} error={errors.projectStartDate}  onChange={setFieldValue} className="form_input inputStyle" placeholder="Project Start Date"/>
                            <div style={{ height: 30,display:"inline-block",verticalAlign:"top",marginLeft:10,marginRight:10}}>To</div>
                            <CustomDatePicker type="text" name='projectEndDate'  value={values.projectEndDate}  error={errors.projectEndDate} onChange={setFieldValue} className="form_input inputStyle" placeholder="Project Start Date"/>
                        </div>

                        <div className="input-title">Technology used list</div>
                        <div style={{marginTop: 20}}>
                            <CustomSelect
                                name = 'technologyUsedList'
                                optionList = {getTechnologyOptions()}
                                value={values.technologyUsedList}
                                onChange={setFieldValue}
                                onBlur={setFieldTouched}
                                error={errors.technologyUsedList}
                                touched={touched.technologyUsedList}
                            />
                        </div>
                        {touched.technologyUsedList && <span className="errorTextStyle" >{errors.technologyUsedList}</span>}



                        <div className="input-title">Project Main Image</div>
                        <div style={{marginTop: 20}}>
                            <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text">Upload</span>
                            </div>
                            <CustomFileUpload
                                name = 'projectMainImage'
                                isMulti={false}
                                value={values.projectMainImage}
                                onChange={setFieldValue}
                                error={errors.projectMainImage}
                                touched={touched.projectMainImage}
                                acceptType="image/svg+xml, image/jpeg, image/png"
                                isEditing = {editingProject !==""}
                            />
                            </div>
                        </div>
                        {touched.projectMainImage && <span className="errorTextStyle" >{errors.projectMainImage}</span>}


                        <div className="input-title" >Project Video Url</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field type="text" name='projectVideoUrl'  className="form_input inputStyle" placeholder="Project Video Url"/>
                        </div>
                        {touched.projectVideoUrl && <span className="errorTextStyle" >{errors.projectVideoUrl}</span>}


                        <div className="input-title">Project Images Example<span style={{fontSize:"0.6rem"}}>sort by image name number(ex '1.png'will be front of '2.png')</span></div>
                        <div style={{marginTop: 20}}>
                            <CustomFileUpload
                                name = 'projectImagesExample'
                                isMulti={true}
                                value={values.projectImagesExample}
                                onChange={setFieldValue}
                                error={errors.projectImagesExample}
                                touched={touched.projectImagesExample}
                                acceptType="image/svg+xml, image/jpeg, image/png"
                                isEditing = {editingProject !==""}

                            />
                        </div>
                        {touched.projectImagesExample && <span className="errorTextStyle" >{errors.projectImagesExample}</span>}


                        <div className="input-title">Project Report</div>
                        <div style={{marginTop: 20}}>
                            <CustomFileUpload
                                name = 'projectReport'
                                value={values.projectReport}
                                onChange={setFieldValue}
                                error={errors.projectReport}
                                touched={touched.projectReport}
                                acceptType="application/pdf"
                                isEditing = {editingProject !==""}
                            />
                        </div>
                        {touched.projectReport && <span className="errorTextStyle" >{errors.projectReport}</span>}

                        <input type="submit" onClick={()=>{if(Object.keys(errors).length !== 0){Swal.fire({icon: 'error', title: 'Input error',text: 'Please fix all error first!'});}}}className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Edit To Firebase"/>
                        {editingProject !== "" && <input type="button" className="btn btn-outline-secondary btn-block" onClick={()=>{deleteFromDatabase();}} value="Delete"/>}
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default ProjectTab;
