import React,{useEffect, useRef} from 'react';
import TitleTextComponent from './TitleTextComponent';
import BlackHoleComponent from './BlackHoleComponent';
import anime from 'animejs/lib/anime.es.js';
import {useSelector} from 'react-redux'


const ExperiencePage = (props)=> {
    const allExperienceScrollableContainerRef = useRef(null);

    var onGoingAnim = [];
    const experienceList  = useSelector(state=>state.experienceList);

    function playExperienceAnimation(){
        var width = allExperienceScrollableContainerRef.current.clientWidth;

        anime.set('.experienceContainer', {
            translateX: function() { return -1*width }
        });
    
    
        onGoingAnim.push(anime({ 
            targets: ".experienceContainer",
            translateX: 0,
            direction: 'normal',
            easing: "easeInOutQuad",
            duration: 1000,
            delay: anime.stagger(800),
            loop: false
        }));

    }

    useEffect(() =>{

        return () =>{
            //clear all running anim
            onGoingAnim.forEach(anim=>{
                anim.seek(anim.duration);
                var index = anime.running.indexOf(anim);
                if (index !== -1) {
                    anime.running.splice(index, 1);
                }
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    var isDate = function(date) {
        return (new Date(date) !== "Invalid Date") && !isNaN(new Date(date));
    }

    return (
        <div className="pageContainer">
            <div className="contentContainer">
                <TitleTextComponent shouldPlayAnim={props.shouldPlayAnim} titleText="Experience" animDoneCallback={()=>{playExperienceAnimation();}} />

                <div ref={allExperienceScrollableContainerRef} className="allExperienceScrollableContainer" onScroll={()=>{props.allowSwipe(false,true);}}>
                    {experienceList.map((experience) => (
                        <div key={experience.id} style={{transform: props.shouldPlayAnim?"translateX(-100%)":"translateX(0)"}}className="experienceContainer">
                            <div className="experienceImageContainer">
                                <div className="experienceImageInnerContainer">
                                    <img alt="institution Icon" src={experience.institutionImageUrl} width="100%" height="auto" />
                                </div>
                            </div>
                            
                            <div className="experienceContentContainer">
                                <div className="experienceTitle">{experience.experienceTitle}</div>
                                <hr className="experienceLine"/>
                                <div className="experienceLocation">{experience.experienceInstitution+`, ${isDate(experience.experienceStartDate) ? new Date(experience.experienceStartDate).toLocaleString('default', { month: 'long' })+ ' '+ new Date(experience.experienceStartDate).getFullYear(): experience.experienceStartDate} to ${isDate(experience.experienceEndDate) ? new Date(experience.experienceEndDate).toLocaleString('default', { month: 'long' })+' '+new Date(experience.experienceEndDate).getFullYear(): experience.experienceEndDate}`}</div>
                                <div className="experienceDesc">
                                    {experience.experienceDesc}
                                </div>
                            </div>
                            
                        </div>
                    ))}
                </div>
            </div>
            <BlackHoleComponent shouldPlayAnim={props.shouldPlayAnim} />
        </div>
    );
}

export default ExperiencePage;
