import React,{useEffect,useContext} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'


const AboutTab = (props) => {

    //context declaration
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    const aboutDetail = useSelector(state=>state.about);

    //yup validation Schema
    const aboutValidationSchema = Yup.object().shape({
        aboutTitle: Yup.string()
          .min(10, 'Title must have at least 10 char.')
          .max(50, 'Title cannot exceed 50 char.')
          .required('Required'),
        aboutDesc: Yup.string()
          .min(50, 'Desc must have at least 50 char.')
          .max(200, 'Desc cannot exceed 200 chat ')
          .required('Required')
      });

    useEffect(()=>{
        

        return () =>{
            
        }
    });


    return (
        <div className="container-fluid" style={{marginTop: 20}}>
            <Formik
            initialValues={{
                aboutTitle: aboutDetail.title,
                aboutDesc: aboutDetail.desc
            }}
            validationSchema={aboutValidationSchema}
            onSubmit={(values,{ resetForm }) => {
                // all validation passed
               //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                });
                firebaseContext.updateAboutDetailToFirebase(values.aboutTitle,values.aboutDesc,(success)=>{
                    Swal.close();
                    if(success){
                        Swal.fire({
                            icon: 'success',
                            title: 'Successfully Uploaded to Firebase',
                            text: 'Updated the about detail to firebase'
                          });
                        resetForm();
                    }else{
                        Swal.fire({
                            icon: 'error',
                            title: 'Oops...',
                            text: 'Something went wrong!'
                          })
                    }
                });
            }}
            enableReinitialize>
                {({ errors, touched }) => (
                    <Form>
                        <div className="input-title" >About Title</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field name="aboutTitle" type="text" id="flowerName_input" className="form_input inputStyle" placeholder="Title" />
                        </div>
                        <span className="errorTextStyle">{errors.aboutTitle}</span>

                        <div className="input-title">About Desc</div>
                        <Field name="aboutDesc" component="textarea"  className="form-control input-text-area-style mt-10" style={{marginTop: 20}} rows="5" id="comment" />
                        <span className="errorTextStyle">{errors.aboutDesc}</span>


                        <input type="submit" className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Changes To Firebase"/>
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default AboutTab;
