import React,{useEffect,useContext} from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import {useSelector} from 'react-redux';
import { FirebaseContext } from '../firebase';
import Swal from 'sweetalert2'
import CustomFileUpload from "./formikCustomComponent/CustomFileUpload"



const ContactTab = (props) => {

    //context declaration
    var firebaseContext= useContext(FirebaseContext);

    //state declaration
    const contactDetail = useSelector(state=>state.contact);

     //yup validation custom function
    function checkIsPDFArray(files){
        let valid = true;
        if (files) {
          for(var file of files){
              
            if (!['application/pdf'].includes(file.type)) {
              valid = false;
            }
          }
        }
        return valid;
    }
     function checkIfFilesAreTooBigArray(files){
        let valid = true;
        if (files) {
            for(var file of files){
                const size = file.size / 1024 / 1024;
                if (size > 10) {
                    valid = false;
                }
            };
        }
        return valid;
    }

    

    //yup validation Schema
    const contactValidationSchema = Yup.object().shape({
        email: Yup.string()
          .email('Email not valid')
          .required('Required'),
        githubUrl: Yup.string()
            .required('Required'),
        linkedInUrl: Yup.string()
            .required('Required'),
        resume: Yup.mixed()
            .nullable()
            .test('is-big-file', 'File is too large(Larger than 10MB)',checkIfFilesAreTooBigArray)
            .test(
            'is-pdf-file',
            'File must be PDF format',
            checkIsPDFArray
        ),
      });

    useEffect(()=>{
        

        return () =>{
            
        }
    });


    return (
        <div className="container-fluid" style={{marginTop: 20}}>
            <Formik
            initialValues={{
                email: contactDetail.email,
                githubUrl: contactDetail.githubUrl,
                linkedInUrl: contactDetail.linkedInUrl,
                resume: []
            }}
            validationSchema={contactValidationSchema}
            onSubmit={(values,{ resetForm }) => {
               //show loading dialog
                Swal.fire({
                    title: 'Uploading To Firebase',
                    html: 'We are processing your request.',
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    timerProgressBar: true,
                    willOpen: () => {
                        Swal.showLoading()
                    }
                })
                firebaseContext.updateContactDetailToFirebase(values.email,values.githubUrl,values.linkedInUrl,values.resume,(success)=>{
                    Swal.close();
                    if(success){
                        Swal.fire({
                            icon: 'success',
                            title: 'Successfully Uploaded to Firebase',
                            text: 'Updated the about detail to firebase'
                          });
                        resetForm();
                    }else{
                        Swal.fire({
                            icon: 'error',
                            title: 'Oops...',
                            text: 'Something went wrong!'
                          })
                    }
                });
            }}
            enableReinitialize>
                {({ values, errors, touched, setFieldValue }) => (
                    <Form>
                        <div className="input-title" >Email</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field name="email" type="text" className="form_input inputStyle" placeholder="Email" />
                        </div>
                        <span className="errorTextStyle">{errors.email}</span>

                        <div className="input-title">Github Url</div>
                            <Field name="githubUrl" className="form-control input-text-area-style mt-10" style={{marginTop: 20}} />
                        <span className="errorTextStyle">{errors.githubUrl}</span>

                        <div className="input-title" >LinkedIn Url</div>
                        
                        <div className="input-row" style={{minHeight: 40}}>
                            <Field name="linkedInUrl" type="text" className="form_input inputStyle" placeholder="Email" />
                        </div>
                        <span className="errorTextStyle">{errors.linkedInUrl}</span>
                        <div className="input-title">Resume</div>

                        <div style={{marginTop: 20}}>
                            <CustomFileUpload
                                name = 'resume'
                                value={values.resume}
                                onChange={setFieldValue}
                                acceptType="application/pdf"
                                isEditing = {true}
                            />
                        </div>
                        {touched.projectReport && <span className="errorTextStyle" >{errors.resume}</span>}


                        <input type="submit" className="btn btn-outline-secondary btn-block" style={{marginTop: 20}} value="Submit Changes To Firebase"/>
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default ContactTab;
