import currentPageReducer from './currentPageReducer.js'
import projectListReducer from './projectListReducer.js'
import educationListReducer from './educationListReducer.js'
import experienceListReducer from './experienceListReducer.js'
import skillListReducer from './skillListReducer.js'
import aboutReducer from './aboutReducer.js'
import isScrollableReducer from './isScrollableReducer.js'




import contactReducer from './contactReducer.js'

import {combineReducers} from 'redux';

const allReducer = combineReducers({
    currentPage: currentPageReducer,
    projectList: projectListReducer,
    educationList: educationListReducer,
    experienceList: experienceListReducer,
    skillList: skillListReducer,
    contact: contactReducer,
    about: aboutReducer,
    isScrollable: isScrollableReducer
})

export default allReducer;