import React, { useRef, useEffect } from 'react';
import pagesEnum from '../enums/pageEnums';
import anime from 'animejs/lib/anime.es.js';
import $ from 'jquery';
import pageEnums from '../enums/pageEnums';

const starIcon = require('../images/IC_Star.svg');



function PageIndicatorComponent(props) {

    const pagesSequence = [pagesEnum.ABOUT,pagesEnum.EDUCATION,pagesEnum.EXPERIENCE,pagesEnum.PROJECT,pagesEnum.SKILLS,pagesEnum.CONTACT];
    
    const filledBarRef = useRef(null);
    const starIconRef = useRef(null);


    function moveStar(value){
        
        anime({
            targets: starIconRef.current,
            translateX: value, // -> '250px'
            rotate: -55,
            duration: 1500,
            easing: 'easeOutElastic',
            update: function(anim) {

                var matrix = $(starIconRef.current).css('transform').replace(/[^0-9\-.,]/g, '').split(',');
                var translateX = matrix[12] || matrix[4];
                var AbsTranslateX = Math.abs(translateX);
                var filledProgressWidth = parseInt(AbsTranslateX,0) >420? 420:AbsTranslateX;
                anime({
                    targets: filledBarRef.current,
                    translateX: filledBarRef.current.clientWidth -filledProgressWidth, // -> '250px'
                    duration: 0,
                    easing: 'linear'
                });
                
              }
          });
    }

    useEffect(()=>{
        var currentPageIndex = pagesSequence.indexOf(props.currentPage);
        moveStar(-90 * currentPageIndex);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    });

    return (
        <div id="LeftFullHeightContainer">
            <div id="pageIndicatorContainer">
                    <div className="pageTitle" style={{marginLeft:0}} onClick={e=>props.changePageCallback(pageEnums.CONTACT)}>
                        Contact
                    </div>
                    <div className="pageTitle" onClick={e=>props.changePageCallback(pageEnums.SKILLS)} >
                        Skills
                    </div>
                    <div className="pageTitle" onClick={e=>props.changePageCallback(pageEnums.PROJECT)}>
                        Project
                    </div>
                    <div className="pageTitle" onClick={e=>props.changePageCallback(pageEnums.EXPERIENCE)}>
                        Experience
                    </div>
                    <div className="pageTitle" onClick={e=>props.changePageCallback(pageEnums.EDUCATION)}>
                        Education
                    </div>
                    <div className="pageTitle" onClick={e=>props.changePageCallback(pageEnums.ABOUT)}>
                        About
                    </div>
                    

                <div>
                    <img ref={starIconRef} id="starIcon" src={starIcon} alt="star" />
                    

                    <div id="progressLine">
                        <div style={{top:0,bottom:0,right:0,left:0,position:'absolute',background:"white",zIndex:-1}}/> 
                        <div ref={filledBarRef} id="progressFilled"/>    
                    </div>
                    
                </div>
                
            </div>
            
        </div>
    );
}

export default PageIndicatorComponent;
